/**
 * Generic Woocommerce styling.
 */


body,
body.woocommerce {

	&.single-product {

		.woocommerce-message
		{
			// Design
			background-color: #fff;
		}

		.yolo-page-title-section {
			// Layout
			margin-bottom: 0;
		}

		.single-product-image {
			// Design
			overflow: hidden;

			.owl-nav {

				& > div {

					&:hover {
						// Text
						color: map-get-deep( $colors, 'brand', 'red' ) !important;
					}
				}
			}
		}

		.single-product-wrap {
			// Layout
			padding-top: 50px;
			padding-bottom: 50px;
			// Design
			background-color: #f6f6f6;

			div {

				&.product {

					.woocommerce-tabs {

						ul {

							&.tabs {

								li {

									&.active {
										// Text
										color: map-get-deep( $colors, 'brand', 'red' );
									}
								}
							}
						}

						.panel {

							h2 {

								&:before {
									// Design
									background-color: map-get-deep( $colors, 'brand', 'red' );
								}
							}
						}
					}
				}
			}
		}

		.summary-product-wrap {

			form {

				&.cart {
					// Mixins
					@include ajaxAddToCartButton;

					.button {
						// Design
						background-color: map-get-deep( $colors, 'brand', 'red' ) !important;

						&:hover {
							// Design
							background-color: darken( map-get-deep( $colors, 'brand', 'red' ), 10% ) !important;
						}
					}

					.variations {
						// Layout
						table-layout: fixed;

						select {
							// Size
							width: 100%;
						}
					}

					.woocommerce-variation-add-to-cart {

						.single_add_to_cart_button {
							// Size
							min-width: 0;
						}
					}
				}
			}
		}
	}
}
