/**
 * 404 page styling.
 */

body {

	&.error404 {
		// Localscoped variables
		$textColor: #333;

		.page404 {
			// Layout
			padding-top: 50px;
			padding-bottom: 130px;
			position: static;

			.content-wrap {
				// Layout
				position: static;
				// Design
				transform: none;
				// Text
				color: $textColor;

				.page404-title {
					// Text
					color: $textColor;

					.p-title {
						// Text
						color: $textColor;
					}

					.p-description {
						// Text
						color: $textColor;
					}
				}

				.p-title-hr {

					.hr-icon {

						&::before,
						&::after {
							// Design
							background-color: $textColor;
						}
					}
				}

				.return {

					a {
						// Design
						border: none !important;

						&:hover {
							// Design
							background-color: darken( map-get-deep( $colors, 'brand', 'red' ), 10% );
						}
					}
				}
			}
		}
	}
}
