.mobile-header
{
	$BEMHelper: &;
	overflow: visible;

	@media ( min-width: 992px )
	{
		display: none;
	}

	&--scrolled-to-partstream-hotspot
	{
		#{ $BEMHelper }
		{
			&__menu-toggle-button
			{
				transform: translateY( -100px );
			}

			&__row
			{
				&--top
				{
					transform: translateY( -100px );
				}
			}
		}
	}

	&__home-link
	{
		display: inline-block;
	}

	&__menu-toggle-button
	{
		$top: 24px;
		background: none;
		border: none;
		left: map-get( $newLayout, 'sidePadding' );
		padding: 0;
		position: fixed;
		top: $top;
		transform: translateY( 0 );
		transition: left 0.3s, transform 0.3s;
		width: 27px;
		z-index: map-get-deep( $zIndex, 'mobileHeader', 'button' );

		@media ( max-width: 782px )
		{

			body.logged-in.admin-bar &
			{
				top: ( 46px + $top );
			}
		}

		@media ( min-width: 783px ) and ( max-width: 991px )
		{

			body.logged-in.admin-bar &
			{
				top: ( 32px + $top );
			}
		}

		body.menu-mobile-in &
		{
			color: #fff;
			left: map-get( $newLayout, 'mobileMenuDynamicWidth' ) + 5;

			@media ( min-width: map-get( $newLayout, 'mobileMenuMaxWidthBreakpoint' ) )
			{
				left: map-get( $newLayout, 'mobileMenuTargetWidth' ) + 30;
			}
		}
	}

	&__menu-toggle-button-label
	{
		font-size: 10px;

		&--mobile-menu-closed
		{
			display: block;

			body.menu-mobile-in &
			{
				display: none;
			}
		}

		&--mobile-menu-open
		{
			display: none;
			left: -3px;
			position: relative;
			white-space: nowrap;

			@media ( min-width: 480px )
			{
				left: -18px;
			}

			body.menu-mobile-in &
			{
				display: block;

				#{ $BEMHelper }
				{
					&__menu-toggle-button-line
					{
						&--first
						{
							background-color: red;
						}

						&--last
						{
							background-color: yellow;
						}

						&--middle
						{
							display: none;
						}
					}
				}
			}

			#{ $BEMHelper }
			{
				&__menu-toggle-button-label-extra-text
				{
					@media ( min-width: 480px )
					{
						display: inline;
					}
				}
			}
		}
	}

	&__menu-toggle-button-label-extra-text
	{
		display: none;
	}

	&__menu-toggle-button-line
	{
		background-color: #000;
		display: block;
		height: 2px;
		margin-bottom: 8px;
		transition: transform 0.3s, width 0.3s;

		&--first
		{
			body.menu-mobile-in &
			{
				transform: rotate( 45deg );
				transform-origin: 0 0;
				width: 109%;
			}
		}

		&--last
		{
			margin-bottom: 4px;

			body.menu-mobile-in &
			{
				transform: rotate( -45deg );
				transform-origin: 0 100%;
				width: 109%;
			}
		}

		&--middle
		{

			body.menu-mobile-in &
			{
				visibility: hidden;
			}
		}

		body.menu-mobile-in &
		{
			background-color: #fff;
		}
	}

	&__row
	{
		background-color: #fff;
		height: 74px;

		&--bottom
		{
			bottom: 100%;
			box-shadow: 0 0 3px 0 rgba( 51, 51, 51, 0.3 );
			left: 0;
			position: absolute;
			right: 0;
			width: 100%;
			z-index: 2;

			.search-form
			{
				height: 100%;

				button[type="submit"]
				{
					height: 100%;

					svg
					{
						height: 24px;
						width: 22px;
					}
				}

				input[type="text"]
				{
					background: none;
					font-size: 19px;
					height: 100%;
					padding-left: map-get( $newLayout, 'sidePadding' );
				}
			}
		}

		&--top
		{
			box-shadow: 0 0 3px 0 rgba( 51, 51, 51, 0.3 );
			display: flex;
			padding: 0 16px 0 map-get-deep( $layout, 'sidePadding', 'mobile' );
			position: relative;
			transform: translateY( 0 );
			transition: transform 0.3s;
			z-index: 3;

			#{ $BEMHelper }
			{
				&__column
				{
					&--first
					{
						padding-top: 23px;
						text-align: left;
						width: 70px;
					}

					&--last
					{
						position: relative;
						text-align: right;
						width: 70px;
					}

					&--middle
					{
						flex: 1 1 auto;
						padding-top: 11px;
						text-align: center;
					}
				}
			}
		}
	}

	&__service-booking-button
	{
		$top: 24px;
		-webkit-appearance: none;
		background: none;
		border: none;
		color: map-get-deep( $colors, 'brand', 'red' );
		left: map-get( $newLayout, 'sidePadding' ) + 40px;
		padding: 0;
		position: fixed;
		text-align: center;
		top: $top;
		z-index: map-get-deep( $zIndex, 'mobileHeader', 'button' );

		@media ( max-width: 782px )
		{

			body.logged-in.admin-bar &
			{
				top: ( 46px + $top );
			}
		}

		@media ( min-width: 783px ) and ( max-width: 991px )
		{

			body.logged-in.admin-bar &
			{
				top: ( 32px + $top );
			}
		}

		svg
		{
			fill: currentColor;
			height: 23px;
			width: 23px;
		}
	}

	&__service-booking-button-text
	{
		display: block;
		font-size: 10px;
		margin-top: -4px;
		white-space: nowrap;
	}

	&__sticky-wrap
	{
		display: block;
		left: 0;
		position: fixed;
		right: 0;
		top: 0;
		width: 100%;
		z-index: map-get-deep( $zIndex, 'mobileHeader', 'base' );

		@media ( max-width: 782px )
		{
			body.logged-in.admin-bar &
			{
				top: 46px;
			}
		}

		@media ( min-width: 783px ) and ( max-width: 991px )
		{
			body.logged-in.admin-bar &
			{
				top: 32px;
			}
		}
	}

	.logo
	{
		$width: 57px;
		$aspectRatio: ( 1288 / 1043 );
		height: #{ $aspectRatio * $width } !important;
		width: $width !important;
	}
}
