/**
 * Generate fadein/fadeout animations for all different display types
 */
@keyframes fadeInBlock {
  0% {
    display: block;
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes fadeOutBlock {
  0% {
    display: block !important;
    opacity: 1; }
  99% {
    display: block !important; }
  100% {
    display: none;
    opacity: 0; } }

@keyframes fadeInFlex {
  0% {
    display: flex;
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes fadeOutFlex {
  0% {
    display: flex !important;
    opacity: 1; }
  99% {
    display: flex !important; }
  100% {
    display: none;
    opacity: 0; } }

@keyframes fadeInInline {
  0% {
    display: inline;
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes fadeOutInline {
  0% {
    display: inline !important;
    opacity: 1; }
  99% {
    display: inline !important; }
  100% {
    display: none;
    opacity: 0; } }

@keyframes fadeInInlineBlock {
  0% {
    display: inline-block;
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes fadeOutInlineBlock {
  0% {
    display: inline-block !important;
    opacity: 1; }
  99% {
    display: inline-block !important; }
  100% {
    display: none;
    opacity: 0; } }

