/*
 * Generic variables.
 *
 *
 * This file should only be used for generic variables. Variables related to for
 * instance specific ACF flexible content and modules of that should be placed
 * within their own respective files.
 */



/**
 * Environment variables.
 */
// If we haven't set the main $environment variable
@if not( variable-exists( 'environment' ) ) {

	// Set environment to null, but use global as we're in an @if which (apparently?!) means it will be scoped otherwise
	$environment: null !global;
}



/**
 * Selectors, IDs, and class names.
 */
$selectors:
(
	'generic':
	(
		'sectionPrefix': 's-',
	),
);



/**
 * Text.
 */
$fonts:
(
	'base'         : 'Open Sans',
	'baseHtmlClass': 'wf-opensans-n4-active',
	'fallback'     : Sans-Serif,
	'heading'      :
	(
		'alt1'         : 'Montserrat',
		'alt1HtmlClass': 'wf-montserrat-n4-active',
		'alt2'         : 'Raleway',
		'alt2HtmlClass': 'wf-raleway-n4-active',
	),
	'newBase'     : 'Raleway',
	'newBaseHtmlClass': 'wf-raleway-n4-active',
);



/**
 * Colors.
 */
$colors:
(
	'brand':
	(
		'red': #780000,
	),
	'pageBackground': #f8f8fa,
);



/**
 * Layout.
 */
$layout:
(
	'sidePadding':
	(
		'mobile': 20px,
	),
);
$newLayout:
(
	'boxedWidth': 1px,
	'boxedWidthNarrow': 800px,
	'mobileMenuDynamicWidth': 83.8%,
	'mobileMenuTargetWidth': 471px,
	'sidePadding': 20px,
);

// Calculate at what breakpoint we need to have a rule for set widths instead of percentual ones
$newLayout: map-merge(
	$newLayout,
	(
		'mobileMenuMaxWidthBreakpoint':
		(
			ceil(
				strip-units( map-get( $newLayout, 'mobileMenuTargetWidth' ) )
				/
				(
					strip-units( map-get( $newLayout, 'mobileMenuDynamicWidth' ) )
					/
					100
				)
			)
			*
			1px
		),
	)
);



/**
 * Timing, transitions, & animations
 */
$transitions:
(
	'base': .3s,
);



/*
 * Z-index is enough of a bitch to deserve having it's own section here. We
 * collect all z-index references here instead of on a section by section
 * basis to make it readable so we know quickly which element is blocking
 * which etc.
 */
$zIndex:
(
	'header':
	(
		'base': 2,
		'cart': 600,
	),
	'mobileHeader':
	(
		'base'  : 298,
		'button': 998,
	),
	'slideshow':
	(
		'buttons': 200,
	),
	'card':
	(
		'card' : 201,
		'text' : 203,
		'img'  : 202,
		'popup': 999,
	),
	'welcomeText':
	(
		'text': 2,
		'img' :
		(
			'bg': 1,
			'fg': 3,
		),
	),
	'footer':
	(
		'image'  : 10,
		'overlay': 20,
		'text'   : 30,
	),
	'404':
	(
		'bgImg'  : 700,
		'content': 800,
	),
	'partstream':
	(
		'mainElement': 1,
	),
	'customBreadcrumb':
	(
		'mainElement': 2,
	),
	'category':
	(
		'information': 2,
	),
);




$popupBoxShadow: 0 2px 4px 0 rgba( 0, 0, 0, 0.12 ), 0 4px 4px 0 rgba( 0, 0, 0, 0.24 );
$popupOverlayBackgroundColor: rgba( 0, 0, 0, 0.5 );
