.call-to-action
{
	$BEMBase: &;
	padding-bottom: 30px;

	@media ( min-width: 1200px )
	{
		text-align: center;
	}

	&--grey
	{
		background-color: #f8f8f8;

		#{ $BEMBase }
		{
			&__button
			{
				@include buttonColorFix();
				color: #fff;
			}
		}
	}

	&--red
	{
		background-color: map-get-deep( $colors, 'brand', 'red' );
		color: #fff;

		#{ $BEMBase }
		{
			&__button
			{
				background-color: #fff;
				color: #000;
				transition: background-color map-get-deep( $transitions, 'base' );

				&:hover
				{
					background-color: darken( #fff, 10% );
				}
			}

			&__title
			{
				color: #fff;
			}
		}
	}

	&--white
	{
		#{ $BEMBase }
		{
			&__button
			{
				@include buttonColorFix();
				color: #fff;
			}
		}
	}

	&__button
	{
		font-family: map-get( $fonts, 'newBase' ), map-get( $fonts, 'fallback' );
		border: none;
		display: inline-block;
		font-size: 19px;
		font-weight: bold;
		letter-spacing: 0.2px;
		margin: 28px 0 0 0;
		padding: 10px;
		text-align: center;
		width: 100%;

		@media ( min-width: 600px )
		{
			max-width: 400px;
		}
	}

	&__text
	{
		font-family: map-get( $fonts, 'newBase' ), map-get( $fonts, 'fallback' );
		font-size: 24px;
		letter-spacing: 0.4px;
		line-height: 1.55;
		margin: 24px 0 0 0;
		padding-left: 1px;
	}

	&__title
	{
		font-family: map-get( $fonts, 'newBase' ), map-get( $fonts, 'fallback' );
		font-size: 30px;
		font-weight: bold;
		letter-spacing: 2px;
		margin: 28px 0 0 0;
		padding-left: 4px;
	}

	&__wrap
	{
		font-family: map-get( $fonts, 'newBase' ), map-get( $fonts, 'fallback' );
		@include sectionPaddingVertical();
		@include sectionWidthNarrow();
		$BEMBase: &;
	}
}
