.woocommerce-messages
{
	@include sectionWidth(
		(
			'useIntermediaryMaxWidths': false,
		)
	);

	.woocommerce-message
	{
		background-color: rgb( 252, 252, 252 );
		margin-bottom: 0px;
		margin-top: 30px;

		.page-template-page_spare-parts:not( .page-child ) &,
		.post-type-archive-product &
		{
			margin-bottom: 30px;
		}
	}
}
