/**
 * Styling for the product quick view popup.
 */


body,
body.woocommerce {

	#popup-product-quick-view-wrapper {
		// Mixins
		@include closePopupButton;

		.modal-backdrop {
			// Size
			height: 100% !important;
			// Design
			overflow: hidden;
		}

		.single-product-image {
			// Design
			overflow: hidden;
		}

		.summary-product-wrap {
			// Mixins
			@include ajaxAddToCartButton;
		}

		.button {
			// Mixins
			@include buttonColorFix( ' !important' );
		}

		.owl-nav {

			& > div {

				&:hover {
					// Design
					color: map-get-deep( $colors, 'brand', 'red' ) !important;
				}
			}
		}
	}
}
