
.woocommerce-pagination
{
	.pagination
	{
		& > li
		{
			& > a,
			& > span
			{
				border: none !important;

				&:hover,
				&:focus,
				&.current,
				&.active
				{
					background-color: map-get-deep( $colors, 'brand', 'red' ) !important;
					border-radius: 0px !important;
					color: #fff;
				}
			}
		}
	}
}

.woocommerce-pagination
{
	border-bottom: 0px solid transparent;
	border-top: 0px solid transparent;
	margin-top: 0;
}
