/**
 * All CSS animations
 */


@keyframes slideDown {

	0% {
		// Layout
		top: -200px;
	}

	100% {
		// Layout
		top: 0;
	}
}





/**
 * Generate fadein/fadeout animations for all different display types
 */
@each $property in ( block, flex, inline, inline-block ) {

	// Create the keyframe name in camelcase
	$fadeIn: _camel-case(
		'fadeIn' +
			_capitalize(
				$property
			)
		);

	// Create the keyframe name in camelcase
	$fadeOut: _camel-case(
		'fadeOut' +
			_capitalize(
				$property
			)
		);


	// Create the animation
	@keyframes #{ $fadeIn } {

		0% {
			// Layout
			display: $property;
			// Design
			opacity: 0;
		}

		100% {
			// Design
			opacity: 1;
		}
	}


	// Create the animation
	@keyframes #{ $fadeOut } {

		0% {
			// Layout
			display: $property !important;
			// Design
			opacity: 1;
		}

		99% {
			// Layout
			display: $property !important;
		}

		100% {
			// Layout
			display: none;
			// Design
			opacity: 0;
		}
	}
}
