
#yolo-content-wrapper {

	.sidebar {

		@include breakpoint( 991px 0 ) {
			// Layout
			clear: both;
			float: none;
			margin: auto;
		}

		@include breakpoint( 991px 481px ) {
			// Size
			width: 100%;
			width: calc( 100% - 60px );
		}

		@include breakpoint( 480px 0 ) {
			// Size
			width: 100%;
			width: calc( 100% - 30px );
		}

		&.col-md-3 {

			@include breakpoint( 992px ) {
				// Size
				width: 25%;
				width: calc( 25% - 33px );
			}
		}

		aside {

			&:before {
				// Design
				background-color: map-get-deep( $colors, 'brand', 'red' );
			}
		}

		.widget_product_categories {

			& > ul {

				& > li {

					a {

						&:hover {

							&:before {
								// Design
								background-color: map-get-deep( $colors, 'brand', 'red' );

							}
						}
					}
				}
			}

			a {

				&.button {
					// Layout
					margin-top: 25px;
					// Text
					white-space: normal;
				}
			}
		}
	}
}
