/**
 * Styling for product listings in Woocommerce.
 */


.woocommerce {

	&.post-type-archive-product,
	&.archive {

		.yolo-page-title-section {
			// Layout
			margin-bottom: 0;
		}

		.site-content-archive-product {
			// Layout
			padding-top: 65px;
			padding-bottom: 65px;
			// Design
			background-color: #f6f6f6;

			.catalog-filter {
				// Layout
				display: none;
			}

			.category-description {
				// Mixins
				@include linkStyling;

				// Layout
				margin: 0 auto 25px auto;
				padding: 20px;
				position: relative;
				left: -3px;
				// Size
				width: 100%;
				width: calc(100% - 39px);
				// Design
				background-color: #fff;
				box-shadow: 0px 0px 1px 0px rgba( 0, 0, 0, 0.15 );

				@include breakpoint( 481px ) {
					// Layout
					margin: 0 0 25px 15px;
					width: calc(99% - 28px);
				}

				@include breakpoint( 480px 0 ) {
					// Size
					width: 100%;
				}

				&:empty {
					// Layout
					display: none;
				}

				:last-child {
					// Layout
					margin-bottom: 0;
				}
			}

			.product-listing {
				// Susy settings
				@include layout( 12 fluid 0.05 );

				// Layout
				display: flex;
				flex-direction: row;
				flex-wrap: wrap;

				.product-item-wrap {
					// Mixins
					@include scaleImageOnHover();

					// Layout
					margin-right: 0 !important;
					margin-left: 0 !important;

					@include breakpoint( 480px 0 ) {
						// Susy settings
						@include span( 6 );
						// Layout
						margin-top: 10px;

						&:nth-of-type( 2n ) {
							// Layout
							padding-right: 0;
							padding-left: 5px;
						}

						&:nth-of-type( 2n-1 ) {
							// Layout
							padding-right: 5px;
							padding-left: 0;
						}
					}

					@include breakpoint( 768px 0 ) {
						// Susy settings
						@include span( 6 );

						&:nth-of-type( 2n ) {
							// Susy settings
							@include last;
						}
					}

					@include breakpoint( 769px ) {
						// Susy settings
						@include span( 4 );

						&:nth-of-type( 3n ) {
							// Susy settings
							@include last;
						}
					}

					// First item
					&:first-of-type,
					&:nth-of-type( 2 ) {
						// Layout
						margin-top: 0;
					}

					// Third item
					&:nth-of-type( 3 ) {

						@include breakpoint( 769px ) {
							// Layout
							margin-top: 0;
						}
					}

					img {
						// Design
						transform: none;
					}

					.product-item-inner {
						// Size
						height: 100%;
					}

					.product-thumb {

						.product-flash-wrap {

							&:empty {
								// Layout
								display: none;
							}

							.product-flash {
								// Design
								background-color: map-get-deep( $colors, 'brand', 'red' );

								&:nth-child( 2 ) {
									// Design
									background-color: #000;
								}
							}
						}
					}

					.product-link {

						.product-hover-sign {

							hr {
								// Design
								border-color: map-get-deep( $colors, 'brand', 'red' );
							}
						}
					}
				}
			}
		}
	}
}
