/**
 * Styling for listing upsell products
 */


body,
body.woocommerce {

	.products {

		&.upsells {

			.widget-title {
				// Design
				border: none !important;
				// Text
				font-size: 25px;
				text-align: center;
				text-transform: uppercase;
			}

			.product-item-wrap {
				// Mixins
				@include scaleImageOnHover();

				img {
					// Design
					transform: none;
				}

				.product-thumb {

					.product-thumb-primary,
					.product-thumb-one {

					}
				}

				.product-link {

					.product-hover-sign {

						hr {
							// Design
							border-color: map-get-deep( $colors, 'brand', 'red' );
						}
					}
				}
			}
		}
	}
}
