/**
 * { item_description }
 */


.s-large-hero-image
{
	.yolo-page-title-wrap
	{
		// Size
		height: auto;

		.yolo-page-title-overlay
		{
			&.toned-down
			{
				// Layout
				background-color: #333;
				opacity: 0.7;
			}
		}

		.container
		{
			// Size
			height: auto;

			.block-center
			{
				// Layout
				padding: 50px 0;
				// Size
				height: auto;

				.heading
				{
					// Size
					max-width: 820px;
					// Text
					text-transform: none;

					&::before,
					&::after
					{
						// Layout
						display: none;
					}
				}

				.ingress,
				{
					&,
					& p
					{
						color: #fff;
						font-size: 22px;
						line-height: 32px;
						text-align: center;

						html.wf-opensans-n4-active &
						{
							// Text
							text-shadow: 2px 2px 5px #222;
						}

						@include breakpoint( 960px 0 )
						{
							// Text
							font-size: 16px;
							line-height: 22px;
						}
					}
				}
			}
		}
	}
}
