/*
 * Generic SCSS-functions.
 *
 * Remember: The difference between a function and a mixin is that a function
 * returns a value while mixins return CSS properties.
 */





/*
 * Prefix a section name so we have a single place to edit the section structure
 *
 * @param      {string}  sectionName          - The name of the section without
 *                                            prefix
 *
 * @return     {string}  The prefixed section name including selector (dot)
 */
@function prefixSection( $sectionName: "" ) {

	@return '.s-' + $sectionName;
}





/*
 * Prefix a wrapper name so we have a single place to edit the wrapper structure
 *
 * @param      {string}  sectionName          - The name of the wrapper without
 *                                            prefix
 *
 * @return     {string}  The prefixed wrapper name including selector (dot)
 */
@function prefixWrapper( $sectionName: "" ) {

	@return '.w-' + $sectionName;
}






/**
 * Check whether a variable has a value.
 *
 * PLEASE NOTE: Due to SCSS not allowing variable name interpolation you should
 * always do a variable-exists() check before cheking if it's empty.
 *
 * @param      {mixed}  $var            - The value of a variable to check
 */
@function empty( $var: null ) {

	// Is it empty?
	@if $var == null or $var == false or $var == '' {

		@return true;

	// Nope, we have a value
	} @else {

		@return false;
	}
}



@function strip-units ( $number )
{
	@return $number / ( $number * 0 + 1 );
}
