/*
 * Welcome text related styling.
 */


.s-welcome-text {
	// Localscoped variables
	$textWidth: 70%;

	// Mixins
	@include parallaxWrap;

	// Layout
	padding: 30px 0;
	position: relative;
	// Design
	background-color: #f4f4f4;
	overflow: hidden;

	.wrapper
	{
		// Layout
		margin: auto;
		padding: 0 15px;
		position: relative;
		// Size
		max-width: 1400px;
		width: 95%;

		@include breakpoint( 1200px 992px )
		{
			// Size
			max-width: 970px;
		}

		@include breakpoint( 991px 768px )
		{
			// Size
			max-width: 750px;
		}
	}

	.heading {
		// Layout
		margin-top: 0;
		margin-bottom: 0;
		position: relative;
		z-index: map-get-deep( $zIndex, 'welcomeText', 'text' );
		// Size
		width: $textWidth;
		// Text
		color: map-get-deep( $colors, 'brand', 'red' );
		font-family: map-get-deep( $fonts, 'heading', 'alt2' ), map-get( $fonts, 'fallback' );
		font-size: 40px;
		text-transform: uppercase;

		@include breakpoint( 991px 0 ) {
			// Size
			width: 100%;
		}
	}

	.subheading {
		color: map-get-deep( $colors, 'brand', 'red' );
		font-family: map-get-deep( $fonts, 'heading', 'alt2' ), map-get( $fonts, 'fallback' );
		margin-bottom: 15px;
		margin-top: 0;
		padding-left: 60px;
		position: relative;
		text-transform: uppercase;
		width: $textWidth;
		z-index: map-get-deep( $zIndex, 'welcomeText', 'text' );

		@include breakpoint( 991px 0 ) {
			// Size
			width: 100%;
		}

		@include breakpoint( 479px 0 ) {
			// Layout
			padding-left: 0;
		}
	}

	.ingress {
		// Layout
		position: relative;
		z-index: map-get-deep( $zIndex, 'welcomeText', 'text' );
		// Size
		width: $textWidth;
		// Text
		color: map-get-deep( $colors, 'brand', 'red' );

		@include breakpoint( 991px 0 ) {
			// Size
			width: 100%;
		}
	}

	.background-img {
		// Layout
		position: absolute;
		top: 7px;
		right: 18%;
		z-index: map-get-deep( $zIndex, 'welcomeText', 'img', 'bg' );
		// Size
		height: 120%;
		width: auto;
		// Design
		opacity: 0.1;

		@include breakpoint( 600px 0 ) {
			// Layout
			top: 10%;
			right: auto;
			left: 50%;
			// Size
			width: 70%;
			height: auto;
			// Design
			opacity: 0.075;
			transform: translateX( -50% );
		}
	}

	.foreground-img {
		// Mixins
		@include parallaxItem( -0.2px, 1.2, 50% );

		// Layout
		position: absolute;
		bottom: 0;
		right: 0;
		z-index: map-get-deep( $zIndex, 'welcomeText', 'img', 'bg' );
		// Size
		width: 20%;

		@include breakpoint( 991px 0 ) {
			// Size
			width: 40%;
			// Design
			opacity: 0.7;
		}

		@include breakpoint( 600px 0 ) {
			// Layout
			display: none;
		}
	}
}
