/**
 * Styling for the PartStream part selection interface.
 */


// Workaround for PartStream setting a general body line height
html body
{
	line-height: 1.8;
}

body
{
	$tableBreakpoint: 390px 0;

	&.ari-noscroll
	{
		overflow: hidden !important;

		#yolo-content-wrapper
		{
			position: static !important;
		}

		#ariPartStream
		{
			position: static !important;
		}
	}

	#ariPartStream
	{
		$clickScrollCompensator: 170px;
		background: none !important;
		margin-bottom: ( - $clickScrollCompensator );
		max-width: none !important;
		min-width: 0 !important;
		padding-top: $clickScrollCompensator;
		position: relative;
		top: ( - $clickScrollCompensator );
		z-index: map-get-deep( $zIndex, 'partstream', 'mainElement' );
		width: 100% !important;

		@media ( min-width: 992px )
		{
			$clickScrollCompensator: 30px;
			margin-bottom: ( - $clickScrollCompensator );
			padding-top: $clickScrollCompensator;
			top: ( - $clickScrollCompensator );
		}

		.ari-product-line.ari-assembly-select,
		.ari-brandsBox
		{
			@include breakpoint( 681px 0 )
			{
				// Layout
				align-items: stretch;
				justify-content: space-between;
			}

			.brandLogoBox,
			.item
			{
				@include breakpoint( 681px 0 )
				{
					// Layout
					margin: 0 0 2% 0;
					// Size
					width: 49%;
					min-width: 0;
					height: auto;
				}
			}
		}

		#ari_brands,
		#ari_brands.ari-brandsBox
		{
			// Layout
			justify-content: center;
		}

		#ariSearchLabel
		{
			// Layout
			// display: none !important;
		}

		#navHeader
		{
			// Layout
			display: none !important;
		}

		.ariSearchBlock
		{
			// Layout
			// display: none;

			~ .ariHR
			{
				// Layout
				// display: none;
			}

			.ariSearchInputBlock
			{
				// Layout
				position: relative;

				&.part-search
				{
					@include breakpoint( 759px )
					{
						// Layout
						padding-left: 10px;
					}
				}

				.ariSearchInput
				{
					// Layout
					padding-right: 3.5em;
					// Size
					width: 100%;
					// Design
					background-color: #f5f5f5;
					border: none;
					// Text
					color: #878787;
				}

				.ari-responsive_btnLookup
				{
					// Layout
					position: absolute;
					bottom: 3px;
					right: 0;
				}
			}


			.ari-responsive_btnLookup
			{
				// Layout
				display: inline-block;
				padding-left: 1em;
				padding-right: 1em;
				right: 4em;
				// Size
				height: 40px;
				// Text
				line-height: 40px;

				.ari-ModelSearchBtn,
				.ari-PartSearchBtn
				{
					// Layout
					padding-bottom: 1px;
				}
			}
		}

		#ari-assemblySrch-wrapper
		{
			.ari-x-box
			{
				// Layout
				top: 0.9em;
			}

			img
			{
				// Layout
				top: 11px;
			}
		}

		#ari-assembSrch-bar
		{
			// Layout
			padding-right: 40px;
			text-overflow: ellipsis;
			// Design
			background-color: #f5f5f5;
			border: none;
			// Text
			color: #878787;
		}

		#ariAssemblyList
		{
			// Susy settings
			@include susy-clearfix;
			// Layout
			margin-bottom: 10px;

			#ariAssemblyLabelContent
			{
				// Text
				font-weight: bold;
			}

			#aari_Assemblies_jl
			{
				// Size
				width: 100% !important;
			}
		}

		#ariAssemblyInfo
		{
			// Susy settings
			@include susy-clearfix;
			// Layout
			margin-bottom: 5px;

			#ariInfoBack
			{
				@include breakpoint( 768px 0 )
				{
					// Layout
					display: block;
					float: none;
					margin-bottom: 10px;
				}
			}

			#ariInfoDesc
			{
				@include breakpoint( 768px 0 )
				{
					// Layout
					display: block;
				}

				@include breakpoint( 600px 0 )
				{
					// Layout
					float: none;
				}
			}

			#ariInfoZoom
			{
				@include breakpoint( 768px 601px )
				{
					// Layout
					display: block;
					float: right;
				}

				@include breakpoint( 600px 0 )
				{
					// Layout
					display: inline-block;
					float: none;
					margin-top: 17px;
					margin-bottom: 10px;
					// Size
					width: auto;
				}
			}
		}

		.ariPLTag
		{
			@include breakpoint( $tableBreakpoint )
			{
				// Layout
				flex: 0 0 auto;
				padding: 5px 13px;
				// Design
				background: map-get-deep( $colors, 'brand', 'red' );
				// Text
				color: #fff;
				font-weight: bold;
				text-align: center;
			}
		}

		.ariPLSku
		{
			@include breakpoint( $tableBreakpoint )
			{
				// Layout
				flex: 1 1 auto;
				padding-left: 9px;
				// Text
				font-weight: bold;
			}
		}

		.ariPLDesc
		{
			@include breakpoint( $tableBreakpoint )
			{
				// Size
				width: 100%;
			}
		}

		.ariPLPrice
		{
			@include breakpoint( $tableBreakpoint )
			{
				// Size
				flex: 1 1 auto;
			}

			&[price^="Call"]
			{
				& ~ .ariPLCart
				{
					span
					{
						// Design
						background-color: transparent;

						&:hover
						{
							// Design
							background-color: transparent;
						}

						&::after
						{
							// Text
							color: transparent !important;
						}
					}
				}
			}
		}

		.ariPLQty
		{
			input
			{
				// Layout
				padding: 0 !important;
				text-align: center !important;
				// Design
				border: 2px solid #eee;
				border-radius: 0;
				// Text
				line-height: 31px;
			}
		}

		.ariHotSpotQty
		{
			// Layout
			vertical-align: top;

			input
			{
				// Size
				height: 35px;
				// Design
				border-radius: 0;
				// Text
				line-height: 31px;
			}
		}

		.ariPLCart span,
		#ariHotSpotToolTipBtn
		{
			// Extends
			@extend .button;
			// Layout
			display: block;
			position: relative;
			vertical-align: top;
			// Design
			background-color: map-get-deep( $colors, 'brand', 'red' );
			cursor: pointer;
			// Size
			height: 35px;
			width: 35px;

			&:hover
			{
				// Design
				background-color: darken( map-get-deep( $colors, 'brand', 'red' ), 10% );
			}

			&.adding-to-cart
			{
				&::after
				{
					// Layout
					display: none !important;
				}
			}

			.ariPartListAddToCart
			{
				// Layout
				position: absolute;
				top: 0;
				left: 0;
				z-index: 21;
				// Size
				min-width: 0;
				width: auto;
				height: 35px;
				width: 35px;
				// Design
				background-image: none !important;
				opacity: 0;
				// Text
				font-size: 0 !important;
			}

			// Spinner, do not remove
			.fa
			{
				// Layout
				display: block;
				position: absolute;
				top: 0;
				left: 0;
				z-index: 22;
				// Size
				height: 35px;
				width: 35px;
				// Text
				color: #fff;
				font-size: 17px;
				line-height: 35px;
				text-align: center;
			}

			&::after
			{
				content: "\f217";
				// Layout
				display: block;
				position: absolute;
				top: 0;
				left: 0;
				z-index: 20;
				// Size
				width: 35px;
				height: 35px;
				// Text
				color: white;
				font-family: "FontAwesome";
				font-size: 17px;
				line-height: 35px;
				text-align: center;
			}
		}

		#ariSearchPanel
		{
			#ariPagingNavPageInputClassID
			{
				// Layout
				padding: 0;
				// Size
				width: 38px;
				// Text
				text-align: center;
			}
		}

		#ari-searchBox
		{
			// Layout
			// display: none !important;
		}

		.dropdown,
		.ariDropDown
		{
			// Size
			width: 100% !important;
			// Design
			background: #f6f6f6;
			border: none;

			a
			{
				// Layout
				padding: 10px 12px 8px 12px;
				position: relative;
				// Design
				background: none;

				&::after
				{
					content: "\f13a";
					// Layout
					display: block;
					position: absolute;
					top: -3px;
					right: 6px;
					// Text
					font-family: "FontAwesome";
					font-size: 25px;
				}
			}
		}

		input
		{
			&[type="button"]
			{
				// Extends
				@extend .button;
				// Layout
				padding: 5px 10px;
				// Design
				border: none;
				// Text
				color: #fff;
			}
		}

		#ariPartSelectPanel
		{
			.ariMainTitle
			{
				// Layout
				display: none !important;
			}
		}

		#ari_fullScreenImg
		{
			// Size
			width: auto;
			height: auto;
			max-width: 95%;
			max-height: 100%;
		}

		.ari_searchResults_GridHeader
		{
			#ari_ModelListHeader
			{
				// Text
				color: #fff;
			}
		}

		#ari_imgModal
		{
			overflow: hidden;
		}

		#ari_imgModal_Content
		{
			border-radius: 0;
			height: auto;
			height: 100%;
			width: 100%;
			margin: 0;
			overflow: hidden;
			padding: 5px;
			width: auto;

			#ari_fullScreenImg
			{
				display: block;
				height: auto;
				max-height: 100%;
				max-width: 100%;
				pointer-events: none;
				touch-action: none;
				width: auto;
			}

			#ari_imgModal_Close
			{
				display: none !important;
			}
		}
	}
}


#ariPartStream #ari_assemblies .ari-browse-catalog .item,
#ariPartStream div#ari_assemblies div.ari-item-showMore,
#ariPartStream ul.ari-hierarchyLvl li,
#ariPartStream .ari-search-results-list li
{
	padding-bottom: 1.4em !important;
	padding-left: 1.7em !important;
	padding-top: 1.4em !important;
}

#close-partstream-modal
{
	@include buttonColorFix();
	border-radius: 0 0 0 17px;
	border: none;
	color: #fff;
	display: none;
	font-size: 22px;
	font-weight: normal;
	height: 45px;
	line-height: 35px;
	padding: 0;
	position: fixed;
	right: 0;
	text-align: center;
	top: 0;
	width: 45px;
	z-index: 999999;

	body.ari-noscroll &
	{
		display: block;
	}

	.fa
	{
		left: 1px;
		position: relative;
		top: -2px;
	}
}