/**
 * Styling for Klarna checkout.
 */


#yolo-wrapper
{
	#klarna-checkout-widget
	{
		// Layout
		margin: auto;
		padding-top: 20px !important;
		// Size
		max-width: 640px;

		& ~ .klarna_checkout.kco-right-col
		{
			@include breakpoint( 800px )
			{
				// Layout
				float: right;
				margin: 0;
			}
		}

		@include breakpoint( 799px 0 )
		{
			// Layout
			padding-right: 10px !important;
			padding-left: 10px !important;
		}

		*
		{
			// Text
			font-size: 14px !important;
		}

		table
		{
			// Layout
			margin-bottom: 0 !important;

			tr
			{
				&#kco-page-shipping
				{
					p
					{
						// Text
						font-weight: bold;
					}

					#shipping_method
					{
						// Size
						width: 100%;
						width: calc( 100% + 85px );

						input[ type="radio" ]
						{
							// Layout
							vertical-align: baseline;
						}

						label
						{
							// Layout
							margin-left: 5px;
						}
					}
				}

				th,
				td
				{
					// Layout
					vertical-align: middle;
					// Size
					height: 60px;

					&:nth-child( 2 ),
					&:last-child
					{
						// Size
						width: 85px;
					}
				}

				th
				{
					// Design
					border-bottom: 2px solid #eee;
				}

				td
				{
					// Design
					border-bottom: 1px solid #eee;


					&.kco-col-desc
					{
						// Text
						font-weight: bold;
					}

					&#kco-page-shipping-total
					{
						// Layout
						vertical-align: top;
					}
				}
			}
		}

		th,
		#kco-page-total
		{
			// Text
			font-size: 15px !important;
		}

		.product-quantity
		{
			.quantity
			{
				label
				{
					// Layout
					display: none !important;
				}

				.qty
				{
					@include breakpoint( 400px 0 )
					{
						// Layout
						padding-left: 10px !important;
						// Size
						width: 45px !important;
					}
				}
			}
		}
	}

	.klarna_checkout.kco-right-col
	{
		@include breakpoint( 800px )
		{
			// Layout
			float: none;
			margin: auto;
		}
	}

	#kco-extra-checkout-fields .mc4wp-checkbox-woocommerce label,
	#ship-to-different-address
	{
		align-items: center;
		display: flex;
	}

	#kco-extra-checkout-fields .mc4wp-checkbox-woocommerce input,
	#ship-to-different-address-checkbox
	{
		flex: 0 0 auto;
		margin-right: 10px !important;
		margin-top: 0 !important;
	}

	#kco-extra-checkout-fields .mc4wp-checkbox-woocommerce span,
	#ship-to-different-address label
	{
		flex: 1 1 auto;
		margin-bottom: 0 !important;
		margin-top: 0 !important;
	}
}
