/**
 * Styling for product listings with selected products.
 */


.s-selected-products {
	// Mixins
	@include sectionWidth(
		(
			'padding': 0,
		)
	);

	// Layout
	padding-top: 50px;
	padding-bottom: 100px;

	@include breakpoint( 480px 0 ) {
		// Layout
		padding-right: 15px;
		padding-left: 15px;
	}

	.heading {
		// Layout
		margin-top: 0;
		margin-bottom: 10px;
		// Text
		font-weight: 600;
		letter-spacing: 5px;
		text-align: center;
		text-transform: uppercase;
	}

	.shortcode-product-wrap,
	.product-wrap {

		&.masonry {

			.product-filters {

				.filter-center {
					// Layout
					margin-bottom: -15px;

					.category-heading {

						&::before {
							// Design
							background-color: map-get-deep( $colors, 'brand', 'red' ) !important;
						}
					}
				}
			}

			.woocommerce {

				&.product-listing {


					.product-item-wrap {
						// Mixins
						@include scaleImageOnHover();

						img {
							// Design
							transform: none;
						}
					}

					.product-link {

						.product-hover-sign {

							hr {
								// Design
								border-color: map-get-deep( $colors, 'brand', 'red' );
							}
						}
					}
				}
			}
		}
	}
}
