/**
 * Styling for the checkout page.
 */

.custom-checkout-message
{
	background: #F6F7F8;
	border-radius: 50px;
	font-size: 16px;
	padding: 25px 35px;
	position: relative;
	margin: 0 20px;

	&--has-icon
	{
		padding-left: 100px;
	}

	&__icon
	{
		height: auto !important;
		left: 30px;
		max-height: 40px !important;
		max-width: 40px !important;
		position: absolute;
		top: 50%;
		transform: translateY(-50%);
		width: auto !important;
	}

	&__text
	{
		:last-child
		{
			margin-bottom: 0 !important;
		}
	}
}

body
{
	&.woocommerce-checkout
	{
		.woocommerce-checkout-info
		{
			max-width: none;
			width: 100%;
		}

		form.checkout_coupon
		{
			max-width: none;
			width: 100%;

			input
			{
				&.input-text
				{
					max-width: none;
					width: 100%;
				}
			}

			.apply-coupon
			{
				width: 100%;
			}
		}

		.entry-content
		{
			.woocommerce
			{
				.woocommerce-checkout-info
				{
					&.returning-customer
					{
						display: none;
					}
				}

				.woocommerce-checkout {

					#customer_details {

						.woocommerce-billing-fields,
						.woocommerce-shipping-fields {

							h3 {
								// Design
								border-color: map-get-deep( $colors, 'brand', 'red' );
							}

							p {

								label {

									.required {
										// Text
										color: map-get-deep( $colors, 'brand', 'red' );
									}
								}
							}
						}

						.form-row {

							&.create-account {
								// Layout
								display: none;
							}
						}
					}

					#order_review_heading {
						// Design
						border-color: map-get-deep( $colors, 'brand', 'red' );
					}

					.woocommerce-checkout-review-order {

						th {
							// Text
							font-weight: bold;
							text-align: left !important;
						}

						td {
							// Text
							text-align: left !important;
						}
					}
				}

				.payment_box_title {
					// Design
					background: none;

					&.active {

						label {
							// Text
							color: map-get-deep( $colors, 'brand', 'red' );
						}
					}

					label {
						// Text
						font-size: 0;
					}

					img {
						// Layout
						display: block;
						left: -9px;
					}
				}

				#payment {

					#place_order {
						// Mixins
						@include buttonColorFix();
					}
				}
			}
		}
	}
}
