/**
 * WooCommerce cart styling.
 */


body,
body.woocommerce {

	// Cart page
	&.woocommerce-cart {

		.cart-collaterals
		{
			h2
			{
				display: none;
			}

			.cart_totals_inner
			{
				float: none;
				padding: 0;
				margin: 0 !important;
				width: 100%;
			}

			.cart_totals
			{
				margin: 0;

				table
				{
					border-bottom-width: 2px !important;
				}
			}
		}

		.cart-collaterals-td
		{
			border: none !important;
			padding-bottom: 0;
			padding-top: 0;

			// Shipping text doesn't get updated by KCO/Shipping Assistant until the checkout page, which means that this may OR MAY NOT show the correct shipping fee for the items in the cart, so better to just remove it altogether since it's sometimes completely wrong
			.woocommerce-shipping-totals,
			.shipping
			{
				display: none !important;
			}
		}

		.cart-form {

			.shop_table {

				&.cart {

					thead {
						background-color: map-get-deep( $colors, 'brand', 'red' ) !important;

						th {
							// Design
							border: none !important;
							// Text
							font-family: 'Open sans' !important;
						}
					}

					.cart_item {

						.product-name {

							img {

								@include breakpoint( 750px 531px ) {
									// Layout
									margin-right: 10px;
								}

								@include breakpoint( 530px 0 ) {
									// Size
									width: 100%;
								}
							}
						}

						.product-subtotal {
							// Design
							border-right: none;
						}

						.product-quantity {

							.quantity {

								label {
									// Size
									width: auto;
								}
							}
						}

						.product-remove {
							// Design
							border-left: none;
						}

						.product-name-inner {
							// Text
							word-break: break-word;

							.cart-sku {
								// Layout
								display: block;
								// Text
								color: #777777;
								font-size: 0.8em;
								font-style: italic;
							}
						}
					}

					.actions
					{
						@include susy-clearfix;

						&.bottom
						{
							border-top: none;
							padding-top: 0;
						}

						&.top
						{
							border-bottom: none;
							padding-bottom: 0;
						}

						.coupon-input-field
						{
							@include breakpoint( 768px 0 )
							{
								width: 100%;
							}
						}

						.button-wrap
						{
							display: flex;
							flex-direction: row;
							flex-wrap: wrap;
							justify-content: space-between;

							@include breakpoint( 768px 0 )
							{
								display: block;
							}
						}

						.button
						{
							@include buttonColorFix();
							text-align: center;

							@include breakpoint( 768px 0 )
							{
								margin-left: auto;
								margin-right: 0;
								margin-top: 5px !important;
								text-align: center;
								width: 100% !important;
							}

							&:not( .hide )
							{
								@include breakpoint( 768px 0 )
								{
									display: block !important;
								}
							}

							&::before
							{
								display: inline-block;
								font-family: "FontAwesome";
								padding-right: 10px;

								@include breakpoint( 768px 0 )
								{
									width: 30px;
								}
							}

							&.apply-coupon
							{
								&::before
								{
									content: "\f00c";
								}
							}

							&.update-cart {
								// Layout
								display: block;
								margin-top: 0;
								// Size
								width: auto;

								&::before {
									content: "\f021";
								}
							}

							&.keep-shopping {
								// Layout
								margin-top: 0;
								// Design
								background-color: #0e9219;

								&:hover {
									// Design
									background-color: darken( #0e9219, 10% );
								}

								&::before {
									content: "\f217";
									// Size
									width: 34px;
									// Text
									font-size: 1.1em;
								}
							}

							&.checkout-button {
								// Layout
								display: inline-block;
								margin-top: 0;
								// Size
								width: auto;

								&::before {
									content: "\f283";
									// Layout
									display: inline-block;
								}
							}
						}
					}
				}
			}
		}

		.cart-collaterals {

			h2 {
				// Design
				border-color: map-get-deep( $colors, 'brand', 'red' );
			}
		}
	}


	// Mini cart
	.shopping-cart-wrapper {

		.widget_shopping_cart_content {

			.cart_list_wrapper {

				li
				{
					.cart-left
					{
						img
						{
							// Layout
							display: block !important;
							margin: 0 !important;
							// Size
							height: auto !important;
							width: 58px !important;
						}
					}

					.cart-right
					{
						// Text
						font-weight: bold !important;
					}
				}

				p {

					&.buttons {

						.button {
							// Layout
							margin-right: 4%;
							margin-bottom: 5px;
							margin-left: 0;

							& + .button {
								// Layout
								margin-left: 0;
							}
						}
					}
				}

				.cart-sku {
					// Layout
					display: block;
					margin-bottom: 8px;
					// Text
					color: #777;
					font-size: 0.8em;
					font-style: italic;
					font-weight: normal;
					line-height: 0.9em;
				}
			}
		}
	}
}
