

.grid
{
	$BEMBase: &;
	@include sectionPaddingVertical();
	@include sectionWidthNarrow();
	@include genericTextStyling();

	.general-text + &
	{
		margin-top: 0;
		padding-top: 0;
	}

	&--column-count-two
	{
		#{ $BEMBase }
		{
			&__item
			{
				flex: 0 0 50%;
				width: 50%;
			}
		}
	}

	&--column-count-three
	{
		#{ $BEMBase }
		{
			&__item
			{
				flex: 0 0 33%;
				width: 33%;
			}
		}
	}

	&--column-count-four
	{
		#{ $BEMBase }
		{
			&__item
			{
				flex: 0 0 25%;
				width: 25%;
			}
		}
	}

	&--column-count-small-desktop-two
	{
		#{ $BEMBase }
		{
			&__item
			{
				@media ( min-width: 768px )
				{
					flex: 0 0 50%;
					width: 50%;
				}
			}
		}
	}

	&--column-count-small-desktop-three
	{
		#{ $BEMBase }
		{
			&__item
			{
				@media ( min-width: 768px )
				{
					flex: 0 0 33%;
					width: 33%;
				}
			}
		}
	}

	&--column-count-small-desktop-four
	{
		#{ $BEMBase }
		{
			&__item
			{
				@media ( min-width: 768px )
				{
					flex: 0 0 25%;
					width: 25%;
				}
			}
		}
	}

	&--column-count-large-desktop-two
	{
		#{ $BEMBase }
		{
			&__item
			{
				@media ( min-width: 992px )
				{
					flex: 0 0 50%;
					width: 50%;
				}
			}
		}
	}

	&--column-count-large-desktop-three
	{
		#{ $BEMBase }
		{
			&__item
			{
				@media ( min-width: 992px )
				{
					flex: 0 0 33%;
					width: 33%;
				}
			}
		}
	}

	&--column-count-large-desktop-four
	{
		#{ $BEMBase }
		{
			&__item
			{
				@media ( min-width: 992px )
				{
					flex: 0 0 25%;
					width: 25%;
				}
			}
		}
	}

	&__item
	{
		flex: 0 0 auto;
		padding: 7.5px;

		img
		{
			max-width: 100%;
		}
	}

	&__item-wrap
	{
		display: flex;
		flex-wrap: wrap;
		margin: -7.5px;
	}

	.alignnone
	{
		margin: 0;
	}
}
