.product-listing-link
{
	$BEMBase: &;
	@mixin columnCalculator( $columns )
	{
		$gridMargin: 0%;

		@if ( 2 < $columns )
		{
			$gridMargin: 2%;
		}
		@else
		{
			$gridMargin: 4%;
		}

		$width: ( 100% - ( $gridMargin * ( $columns - 1 ) ) ) / $columns;
		margin-left: $gridMargin;
		flex: 0 0 $width;
		width: $width;

		// Reset previous breakpoints
		&:nth-of-type( 1n )
		{
			margin-left: $gridMargin;
			margin-top: 0;
		}

		// First in row shouldn't have margin
		&:nth-of-type( #{ $columns + 'n-' + ( $columns - 1 )  } )
		{
			margin-left: 0;
		}

		// Add top margins after first row
		&:nth-of-type( #{ '1n+' + ( $columns + 1 ) } )
		{
			margin-top: $gridMargin;
		}
	}
	@include columnCalculator( 2 );
	font-family: map-get( $fonts, 'newBase' ), map-get( $fonts, 'fallback' );
	display: block;
	overflow: hidden;
	position: relative;

	&:hover
	{
		#{ $BEMBase }
		{
			&__hover-sign
			{
				hr
				{
					&:nth-child( 1 )
					{
						transform: translate3d(-50%, -50%, 0) rotate(90deg) scale3d(1, 1, 1);
					}

					&:nth-child( 2 )
					{
						transform: translate3d(-50%, -50%, 0) rotate(0) scale3d(1, 1, 1);
					}
				}
			}
		}

		.product-thumb
		{
			.product-thumb-one,
			.product-thumb-primary
			{
				img
				{
					transform: scale( 1.3 ) !important;
				}
			}
		}
	}

	@media ( min-width: 768px )
	{
		@include columnCalculator( 3 );
	}

	@media ( min-width: 992px )
	{
		@include columnCalculator( 4 );
	}

	&__information
	{
		display: block;
		padding-left: 0 !important;
		padding-right: 0 !important;
		padding-top: 8px !important;
		text-align: left !important;
	}

	&__thumbnail
	{
		border: 1px solid #ebebeb;
		display: block;
	}

	&__hover-sign
	{
		width: 50px;
		height: 50px;
		display: block;
		position: absolute;
		z-index: 12;
		top: 50%;
		transform: translate3d(0,-50%,0);
		left: 0;
		right: 0;
		margin-left: auto;
		margin-right: auto;

		hr
		{
			top: 50%;
			-webkit-transition: all .35s ease;
			-o-transition: all .35s ease;
			transition: all .35s ease;
			-webkit-transform: translate3d(-50%,-50%,0);
			transform: translate3d(-50%,-50%,0);
			transform-origin: 50%;
			width: 40px;
			// opacity: 0;
			margin: 0;
			left: 50%;
			position: absolute;
			border: 0;
			border-top: 1px solid map-get-deep( $colors, 'brand', 'red' );
			height: 0;
			z-index: 11;

			&:nth-child( 1 )
			{
				transform: translate3d(-50%,-50%,0) rotate(90deg) scale3d(0,0,1);
			}

			&:nth-child( 2 )
			{
				transform: translate3d(-50%,-50%,0) rotate(0) scale3d(0,0,1);
			}
		}
	}

	.price
	{
		del
		{
			opacity: 0.6;
			font-size: 0.9em;
		}

		ins
		{
			color: map-get-deep( $colors, 'brand', 'red' );
		}
	}

	.product-link
	{
		visibility: visible !important;
	}

	.product-thumb
	{
		.product-thumb-one,
		.product-thumb-primary
		{
			img
			{
				transform: scale( 1 ) !important;
			}
		}
	}

	.woocommerce-loop-product__title
	{
		font-family: map-get( $fonts, 'newBase' ), map-get( $fonts, 'fallback' );
		color: #000 !important;
		font-size: 21px !important;
		font-weight: bold;
		margin: 0 !important;
		overflow: hidden;
		text-align: left;
		text-overflow: ellipsis;
		white-space: nowrap;

		@media ( max-width: 599px )
		{
			font-size: 16px !important;
		}
	}

	.woocommerce-Price-amount
	{
		font-family: map-get( $fonts, 'newBase' ), map-get( $fonts, 'fallback' );
		text-align: left;
		font-weight: normal;
	}
}
