.shop-banner
{
	$BEMBase: &;
	font-family: map-get( $fonts, 'newBase' ), map-get( $fonts, 'fallback' );
	background: #000;
	color: #fff;
	font-size: 18.5px;
    padding: 3px 0 5px 0;
	text-align: center;

	&__inner-wrap
	{
		@include sectionWidth(
			(
				'useIntermediaryMaxWidths': false,
			)
		);
	}
}
