/**
 * Generic Woocommerce styling.
 */


body,
body.woocommerce {

	a[href*="/mitt-konto"] {
		// Layout
		display: none !important;
		// Design
		opacity: 0 !important;
		visibility: hidden !important;
	}


	input,
	a {

		&.button {
			// Mixins
			@include buttonColorFix();
		}
	}

	.product-actions {

		.product-quick-view,
		.add-to-cart-wrap a,
		.add-to-cart-wrap .add_to_cart_button {

			&:hover {
				// Design
				background-color: map-get-deep( $colors, 'brand', 'red' ) !important;
			}

			&.active {
				// Design
				background-color: map-get-deep( $colors, 'brand', 'red' ) !important;
			}
		}

		.add-to-cart-wrap .added_to_cart,
		.add-to-cart-wrap .added_to_cart.wc-forward {
			// Design
			background-color: map-get-deep( $colors, 'brand', 'red' ) !important;

			&:hover {
				// Design
				background-color: map-get-deep( $colors, 'brand', 'red' ) !important;
			}
		}
	}

	.yolo-page-title-section {

		.block-center-inner {

			h1 {
				color: #fff;
				font-family: map-get-deep( $fonts, 'heading', 'alt2' ), map-get( $fonts, 'fallback' );
				font-size: 50px;
				font-weight: bold;
				position: relative;
				text-align: center;
				text-shadow: 2.5px 3px 5px rgba(30,30,30,0.6);
				text-transform: uppercase;

				&::before,
				&::after {
					content: '';
					// Layout
					display: block;
					position: absolute;
					right: 0;
					left: 0;
					// Size
					width: 100%;
					height: 2px;
					// Design
					background-color: #fff;
					box-shadow: 1.5px 2px 3px rgba(30,30,30,0.6);
				}

				&::before {
					// Layout
					top: 0;
				}

				&::after {
					// Layout
					bottom: 0;
				}
			}
		}
	}

	.product-flash-wrap {
		// Layout
		top: -30px !important;
		left: -530px !important;
		padding-top: 75px !important;
		// Size
		width: 1000px !important;
		// Design
		background-color: map-get-deep( $colors, 'brand', 'red' ) !important;
		transform: rotate( -45deg ) !important;
		transform-origin: 50% 0% !important;

		&:empty {
			// Layout
			display: none;
		}

		.product-flash {
			// Layout
			display: block !important;
			margin-top: 0 !important;
			margin-right: auto !important;
			margin-left: auto !important;
			// Design
			background: none !important;
		}
	}

	.product-thumb-one,
	.product-thumb-primary {

		img {
			// Layout
			padding-top: 0 !important;
		}
	}


	.woocommerce-no-products {
		// Layout
		margin: 110px auto !important;
		// Size
		min-height: 0 !important;
		max-width: 80%;
		// Text
		font-weight: bold;
		text-align: center;
	}


	.s-product-recommendations {
		// Mixins
		@include sectionWidth(
			(
				'padding': 0,
			)
		);
	}


	.woocommerce {

		input {

			&.button {

				&:hover {
					// Design
					background-color: map-get-deep( $colors, 'brand', 'red' );
				}
			}
		}
	}

	#payment {

		#place_order {

			&:hover {
				background-color: map-get-deep( $colors, 'brand', 'red' );
			}
		}
	}

	// Reset variations buttons doesn't fit into the theme or have any real use for the type of variable products we're using, so hide those
	.reset_variations {
		// Layout
		display: none !important;
	}

	.single-product-info
	{
		.summary-product
		{
			p.price
			{
				del
				{
					font-size: 0.9em;
					opacity: 0.6;
				}

				ins
				{
					color: map-get-deep( $colors, 'brand', 'red' ) !important;
				}
			}
		}
	}
}
