/*
 * Styling for the general footer.
 */


.s-footer {
	// Layout
	padding: 60px;
	position: relative;
	// Design
	background-size: cover;
	background-position: 50% 50%;

	@media print {
		// Layout
		display: none !important;
	}

	&::before {
		// Mixins
		@include absoluteFill;

		content: '';
		// Layout
		display: block;
		z-index: map-get-deep( $zIndex, 'footer', 'overlay' );
		// Design
		background-color: rgba(31, 31, 31, 0.9);
	}

	.background {
		// Layout
		position: absolute;
		top: 50%;
		left: 50%;
		z-index: map-get-deep( $zIndex, 'footer', 'image' );
		// Size
		min-width: 100%;
		min-height: 100%;
		max-width: none;
		max-height: none;
		// Design
		transform: translate(-50%,-50%);
	}

	.columns {
		// Susy settings
		@include layout( 12 fluid 0.25 );
		@include susy-clearfix;
		// Layout
		margin: auto;
		position: relative;
		z-index: map-get-deep( $zIndex, 'footer', 'text' );
		// Size
		max-width: 1400px;

		.column {
			// Layout
			position: relative;
			// Text
			color: #9b9d9f;

			@include breakpoint( 768px ) {
				// Susy settings
				@include span( 3 );
			}

			@include breakpoint( 767px 0 ) {
				// Susy settings
				@include span( 12 );
				@include last;
			}

			&:first-of-type {

				div {
					// Layout
					display: flex;
					flex-direction: row;
					flex-wrap: nowrap;

					.icon,
					.fa {
						// Size
						width: 35px;
						// Text
						color: #fff;
						font-size: 25px;
						line-height: 25px;

						&.fa-mobile {
							// Text
							font-size: 32px;
						}

						&.fa-envelope-o {
							// Text
							font-size: 20px;
						}
					}

					a {

						&[href^='mailto:'] {
							// Layout
							display: block;
							padding-left: 15px;
							position: relative;

							&:before {
								content: '\f105';
								// Layout
								position: absolute;
								left: 0px;
								// Text
								font-family: 'FontAweSome';
							}
						}
					}
				}
			}

			&:not( :first-of-type ) {

				@include breakpoint( 767px 0 ) {
					// Layout
					margin-top: 40px;
				}
			}

			&:last-of-type {

				@include breakpoint( 768px ) {
					// Susy settings
					@include last;
				}

				.sub-menu {
					// Layout
					padding-left: 25px;
				}
			}

			&:nth-of-type( 3 ),
			&:last-of-type {

				ul {
					// Layout
					padding: 0;
					// Design
					list-style-type: none;

					li {

						a {
							// Layout
							display: block;
							margin-bottom: 20px;
							padding-left: 15px;
							position: relative;

							&:before {
								content: '\f105';
								// Layout
								position: absolute;
								left: 0px;
								// Text
								font-family: 'FontAweSome';
							}
						}
					}
				}
			}


			.heading {
				// Layout
				margin-top: 0;
				margin-bottom: 30px;
				// Text
				color: #fff;
				font-family: map-get-deep( $fonts, 'heading', 'alt1' ), map-get( $fonts, 'fallback' );
				font-size: 18px;
				text-transform: uppercase;
			}
		}
	}

	.payment-methods {
		// Layout
		display: flex;
		position: relative;
		z-index: map-get-deep( $zIndex, 'footer', 'text' );
		// Size
		width: 100%;

		@include breakpoint( 767px 0 ) {
			// Layout
			margin-top: 20px;
		}

		.payment-icon {
			// Layout
			margin-left: 13px;

			&:first-child {
				// Layout
				margin-left: auto;

				@include breakpoint( 767px 0 ) {
					// Layout
					margin-left: 0;
				}
			}

			&.fa {
				// Text
				color: #fff;
				font-size: 34px;
			}

			&.img {
				// Size
				width: auto;
				height: 30px;
			}
		}
	}

	#wiseweb-links {
		// Layout
		margin-top: 70px;
		padding: 0 30px;
		position: relative;
		z-index: map-get-deep( $zIndex, 'footer', 'text' );
		// Text
		color: #9b9d9f;
		text-align: center;
	}
}
