.category-links
{
	$BEMBase: &;
	@include sectionWidth(
		(
			'useIntermediaryMaxWidths': false,
		)
	);
	padding-top: 0;

	@media ( min-width: 768px )
	{
		padding-top: 52px;
	}

	.tax-product_cat &
	{
		padding-top: 52px;

		@media ( min-width: 768px )
		{
			padding-top: 52px;
		}
	}

	.page-template-page_spare-parts:not( .page-child ) &
	{
		padding-bottom: 0;
		padding-top: 0;

		@media ( min-width: 768px )
		{
			padding-bottom: 52px;
			padding-top: 52px;
		}
	}

	@media ( min-width: 768px )
	{
		display: flex;
		flex-wrap: wrap;
	}
}
