/**
 * Styling for listing upsell products
 */


body,
body.woocommerce {

	.cross-sells {

		.widget-title {
			// Design
			border: none !important;
			// Text
			font-size: 25px;
			text-align: center;
			text-transform: uppercase;
		}

		.woocommerce.slider {
			// Layout
			padding-top: 0 !important;
			padding-bottom: 0 !important;
		}

		.product-item-wrap {
			// Mixins
			@include scaleImageOnHover();

			img {
				// Design
				transform: none !important;
			}

			.product-link {

				.product-hover-sign {

					hr {
						// Design
						border-color: map-get-deep( $colors, 'brand', 'red' );
					}
				}
			}
		}
	}
}
