.image-with-text
{
	$BEMBase: &;
	@include sectionWidthNarrow();
	@include genericTextStyling();
	// height: 0;
	overflow: hidden;
	position: relative;

	h2
	{
		font-size: 30px;
		font-weight: bold;
	}

	&__text
	{
		color: #fff;
		font-weight: bold;
		text-align: center;
	}

	&__text-wrapper
	{
		left: 50%;
		padding: 15px;
		position: absolute;
		top: 50%;
		transform: translate( -50%, -50% );
		width: 100%;
	}
}
