.sidescrolling-cards
{
	$BEMBase: &;
	$sideMargin: 15px;
	@include sectionPaddingVertical();
	@include sectionWidth(
		(
			'useIntermediaryMaxWidths': false,
		)
	);
	overflow: hidden;
	width: 100%;

	&__android-width-fixer
	{
		margin-left: - $sideMargin;
		margin-right: - $sideMargin;
		width: auto;

		@media ( min-width: 768px )
		{
			margin-right: auto;
			margin-left: auto;
			width: 100%;
		}
	}

	&__card-title
	{
		font-family: map-get( $fonts, 'newBase' ), map-get( $fonts, 'fallback' );
		color: #fff;
		font-size: 19px;
		font-weight: 700;
		letter-spacing: 0px;
		margin: 0;
		max-height: 2.45em;
		overflow: hidden;
		padding: 0 0 6px 0;
	}

	&__card-subtitle
	{
		font-family: map-get( $fonts, 'newBase' ), map-get( $fonts, 'fallback' );
		color: #fff;
		font-size: 12px;
		letter-spacing: 0px;
		margin: 0;
		padding-bottom: 19px;
		padding-right: 2px;
	}

	&__icon
	{
		$size: 13px;
		display: inline-block;
		height: $size;
		left: 1px;
		margin: 0;
		position: relative;
		top: 2px;
		width: $size;

		@media ( min-width: 450px )
		{
			$size: 23px;
			height: $size;
			top: 6px;
			width: $size;
		}
	}

	&__icon-wrapper
	{
		$size: 40px;
		display: inline-block;
		background: #fff;
		border-radius: 50%;
		height: $size;
		margin: 0 0 10px 0;
		line-height: $size;
		text-align: center;
		width: $size;

		@media ( min-width: 450px )
		{
			$size: 67px;
			height: $size;
			line-height: $size;
			margin: 0 0 17px 0;
			width: $size;
		}
	}

	&__image
	{
		left: 50%;
		max-height: none !important;
		max-width: none !important;
		min-height: 0 !important;
		min-width: 0 !important;
		position: absolute;
		top: 50%;
		transform: translate( -50%, -50% );

		&--match-height
		{
			height: 100% !important;
		}

		&--match-width
		{
			width: 100% !important;
		}
	}

	&__link
	{
		display: block;
		flex: 0 0 37%;
		margin-right: 20px;
		position: relative;
		width: 37%;

		@media ( min-width: 992px )
		{
			flex: 0 0 27%;
			width: 27%;
		}

		&--last
		{
			margin-right: 0;
		}
	}

	&__link-inner-wrap
	{
		height: 0;
		overflow: hidden;
		position: relative;
		padding-bottom: percentage( 250 / 207 );
	}

	&__show-all-link
	{
		font-family: map-get( $fonts, 'newBase' ), map-get( $fonts, 'fallback' );
		$width: 100px;
		color: map-get-deep( $colors, 'brand', 'red' );
		flex: 0 0 $width;
		font-size: 20px;
		font-weight: 600;
		letter-spacing: 0px;
		line-height: 24px;
		padding-right: 2px;
		text-align: right;
		text-decoration: underline;
		vertical-align: top;
		width: $width;

		&:hover
		{
			text-decoration: underline;
		}
	}

	&__scroll-wrapper
	{
		display: flex;
		max-width: none;
		min-width: 100%;
		overflow-y: hidden;
		scrollbar-width: none;
		width: auto;
		-webkit-overflow-scrolling: touch;

		&::-webkit-scrollbar
		{
			display: none;
		}
	}

	&__scroll-wrapper-mobile-spacer
	{
		flex: 0 0 15px;
		width: 15px;

		@media ( min-width: 768px )
		{
			display: none;
		}
	}

	&__scroll-shadow
	{
		box-shadow: 0 0 27px 30px #fff;
		display: none;
		flex: 0 0 0px;
		opacity: 0;
		pointer-events: none;
		position: relative;
		touch-action: none;
		transition: opacity 0.3s;
		width: 0;
		z-index: 2;

		@media ( min-width: 768px )
		{
			display: block;
		}
	}

	&__shadow-wrapper
	{
		display: flex;
		overflow: hidden;

		&--at-beginning
		{
			#{ $BEMBase }
			{
				&__scroll-shadow--right
				{
					opacity: 1;
				}
			}
		}

		&--at-end
		{
			#{ $BEMBase }
			{
				&__scroll-shadow--left
				{
					opacity: 1;
				}
			}
		}

		&--in-middle
		{
			#{ $BEMBase }
			{
				&__scroll-shadow--left
				{
					opacity: 1;
				}

				&__scroll-shadow--right
				{
					opacity: 1;
				}
			}
		}
	}

	&__text-inner-wrap
	{
		bottom: 0;
		left: 0;
		position: absolute;
		right: 0;
		text-align: center;
		width: 100%;

		&--bottom-shadow
		{
			background-image: linear-gradient( 0deg, rgba( 0, 0, 0, 0.7 ), transparent );
		}
	}

	&__text-outer-wrap
	{
		bottom: 0;
		height: 100%;
		left: 0;
		position: absolute;
		right: 0;
		text-align: center;
		width: 100%;

		&--overlay
		{
			background-color: rgba( 0, 0, 0, 0.03 );
		}
	}

	&__title
	{
		font-family: map-get( $fonts, 'newBase' ), map-get( $fonts, 'fallback' );
		flex: 1 1 auto;
		font-size: 24px;
		font-weight: 600;
		letter-spacing: 0.4px;
		margin-bottom: 0;
		margin-top: 0;
		padding-left: 4px;
	}

	&__top-row
	{
		display: flex;
		margin-bottom: 24px;
		padding-left: $sideMargin;
		padding-right: $sideMargin;

		@media ( min-width: 768px )
		{
			padding-left: 0;
			padding-right: 0;
		}
	}
}
