/*
 * Google Maps related styling.
 */

.s-google-map {
	// Mixins
	@include sectionWidth();
	@include linkStyling;

	// Layout
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	margin-bottom: 20px;

	.map {
		// Layout
		padding-bottom: 40%;
		// Size
		min-height: 300px;
		width: 60%;

		@include breakpoint( 700px 0 ) {
			// Size
			width: 100%;
		}
	}

	.info-wrap {
		// Layout
		display: flex;
		align-items: center;
		padding: 15px;
		// Size
		width: 40%;
		// Design
		background-color: #f3f4f4;

		@include breakpoint( 700px ) {
			// Layout
			padding: 15px 30px;
		}

		@include breakpoint( 700px 0 ) {
			// Size
			width: 100%;
		}
	}
}
