.category-image
{
	$BEMBase: &;
	height: 100%;
	position: relative;
	width: 100%;

	&__background
	{
		background-size: cover;
		background-position: center center;
		height: 100%;
		position: absolute;
		width: 100%;
		z-index: 2;

		&--toned-down
		{
			&::before
			{
				background: rgba( 0, 0, 0, 0.45 );
				bottom: 0;
				box-shadow: 0 -30px 50px 0 inset rgba( 0, 0, 0, 0.7 );
				content: '';
				height: 100%;
				left: 0;
				position: absolute;
				right: 0;
				top: 0;
				width: 100%;
				z-index: 3;

				@media ( min-width: 768px )
				{
					box-shadow: 0 -10px 15px 0 inset rgba( 0, 0, 0,.7 );
				}
			}
		}
	}

	&__foreground
	{
		background-size: contain;
		background-position: center center;
		height: 90%;
		left: 5%;
		position: absolute;
		top: 5%;
		width: 90%;
		z-index: 4;
	}
}
