
// Use body to increase specificity and avoid having to use !important too often
body {

	// Main wrapper should be white as standard
	#yolo-content-wrapper {
		// Design
		background-color: #fff !important;
	}


	// Original theme's font was only available in 400 regular, so screw that and give us Open Sans instead
	.motor-testimonial h3 .motor-post h3,
	.motor-ourteam h6,
	.motor-icon-box h3,
	.p-font {
		font-family: map-get( $fonts, 'base' ), map-get( $fonts, 'fallback' );
	}

	.pagination > li > a.next,
	.pagination > li > span.next {
		width: 80px;
	}

	.pagination > li > a.prev,
	.pagination > li > span.prev {
		width: 100px;
	}
}
