/*
 * Logotype list related styling.
 */


.s-logotype-list {
	// Susy settings
	@include layout( 12 fluid 0.25 );
	@include susy-clearfix;

	// Mixins
	@include sectionWidth;

	// Layout
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	justify-content: center;
	padding-top: 7.4%;
	padding-bottom: 7.4%;

	body.single-product & {
		// Layout
		padding-top: 90px;
		padding-bottom: 90px;
	}

	.logotype-item {
		// Layout
		// align-items: center;
		display: block;
		// flex-direction: row;
		// flex-wrap: nowrap;
		flex: 0 0 auto;
		margin-top: 1.75%;
		// Design
		border: 1px solid #e8e8e8;

		body.browser-family-edge & {
			// Layout
			margin-top: 6px;
		}

		@include breakpoint( 992px ) {
			// Susy settings
			@include span( 2.4 );

			// Layout
			margin-top: 0;
		}

		@include breakpoint( 991px 600px ) {
			// Susy settings
			@include span( 4 );

			&:first-child,
			&:nth-child( 2 ),
			&:nth-child( 3 ) {
				// Layout
				margin-top: 0;
			}
		}

		@include breakpoint( 599px 0 ) {
			// Susy settings
			@include span( 6 );

			&:first-child,
			&:nth-child( 2 ) {
				// Layout
				margin-top: 0;
			}
		}

		&:last-child {

			@include breakpoint( 992px ) {
				// Susy settings
				@include last;
			}
		}

		&:nth-child( 3n ) {

			@include breakpoint( 991px 600px ) {
				// Susy settings
				@include last;
			}
		}

		&:nth-child( 2n ) {

			@include breakpoint( 599px 0 ) {
				// Susy settings
				@include last;
			}
		}

		img {
			// Layout
			display: block;
			padding: 10px;
			// Size
			max-width: 100%;
		}
	}
}
