/*
 * Mixins. 'Nuff said.
 */



@mixin boxedLayout()
{
	display: block;
	max-width: 1200px;
	margin-left: auto;
	margin-right: auto;
	padding-left: map-get( $newLayout, 'sidePadding' );
	padding-right: map-get( $newLayout, 'sidePadding' );
	width: 100%;
}




/**
 * Fill out a relative element using an absolute position.
 *
 * @param      {px-value}   width           - How much of the width to fill out
 * @param      {px-value}   height          - How much of the height to fill out
 * @param      {shorthand}  margin          - Any margins (Optional)
 */
@mixin absoluteFill( $width: 100%, $height: 100%, $margin: "" ) {
	// Layout
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	// Size
	width: $width;
	height: $height;

	@if $margin != "" {
		margin: $margin;
	}
}





/**
 * Proper clearing the easy way.
 */
@mixin clearfix {

	&:before,
	&:after {
		content: "";
		// Layout
		display: table;
		clear: both;
	}
}





/**
 * Generate a a box-shadow in material design style.
 *
 * @param      {integer}  level                    - How high up the box-shadow
 *                                                 should appear
 * @param      {integer}  offsetTop                - How much we want to move
 *                                                 the shadow downwards from
 *                                                 it's original position. Avoid
 *                                                 using this argument when
 *                                                 possible to maintain
 *                                                 integrity of material design
 *                                                 pattern.
 * @param      {integer}  opacityModifier          - How much we want to change
 *                                                 the shadow opacity. Avoid
 *                                                 using this argument when
 *                                                 possible to maintain
 *                                                 integrity of material design
 *                                                 pattern.
 */
@mixin materialBoxShadow( $level, $offsetTop: 0, $opacityModifier: 1 ) {

	@if $level == 1 {
		// Design
		box-shadow: 0 ( 1px + $offsetTop ) 3px rgba( 0, 0, 0, ( 0.12 * $opacityModifier ) ), 0 1px 2px rgba( 0,0,0, ( 0.24 * $opacityModifier ) );
	} @else if $level == 2 {
		// Design
		box-shadow: 0 ( 3px + $offsetTop ) 6px rgba( 0, 0, 0, ( 0.16 * $opacityModifier ) ), 0 3px 6px rgba( 0, 0, 0, ( 0.23 * $opacityModifier ) );
	} @else if $level == 3 {
		// Design
		box-shadow: 0 ( 10px + $offsetTop ) 20px rgba( 0, 0, 0, ( 0.19 * $opacityModifier ) ), 0 6px 6px rgba( 0, 0, 0, ( 0.23 * $opacityModifier ) );
	} @else if $level == 4 {
		// Design
		box-shadow: 0 ( 14px + $offsetTop ) 28px rgba( 0, 0, 0, ( 0.25 * $opacityModifier ) ), 0 10px 10px rgba( 0, 0, 0, ( 0.22 * $opacityModifier ) );
	} @else if $level == 5 {
		// Design
		box-shadow: 0 ( 19px + $offsetTop ) 38px rgba( 0, 0, 0, ( 0.30 * $opacityModifier ) ), 0 15px 12px rgba( 0, 0, 0, ( 0.22 * $opacityModifier ) );
	}
}





/**
 * Quick and dirty way of changing text on buttons by means of font-size hacks
 * and pseudo-elements. Not usability-friendly, but very effeicient!
 *
 * @param      {string}  text            - The new button text
 */
@mixin changeButtonText( $text: "" ) {
	// Text
	font-size: 0 !important;

	&::after {
		// Content
		content: $text;
		// Layout
		display: inline;
		// Text
		font-size: 1rem;
	}
}





/**
 * Simulate a long shadow by stacking a given amount of box shadows.
 *
 * @param      {string}          type            - Determine whether to use
 *                                               box-shadow or text-shadow
 * @param      {integer}         stack           - How many times the shadow
 *                                               should stack. This determines
 *                                               the shadow length.
 * @param      {string}          color           - The shadow color
 * @param      {integer|string}  spread          - How much the shadow should
 *                                               spread
 * @param      {integer|string}  grow            - How much the shadow should
 *                                               grow
 * @param      {string}          prefix          - Allows the use of
 *                                               other/previous box-shadows by
 *                                               adding them first in the shadow
 *                                               argument stack
 */
@mixin castLongShadow( $type: "box", $stack: 10, $color: #333, $spread: 0, $grow: 0, $prefix: "" ) {

	// Instantiate value
	$value: if( $prefix != "", $prefix + ", ", "" );

	// Settings for spread, growth, and color (Same every time, so we needn't do this in the actual loop)
	$settings: $spread + "px " + if( $type == "box", $grow + "px ", "" ) + $color;

	// Loop stack to create shadow
	@for $i from 1 through $stack {

		// Create offset and add settings
		$value: $value + $i + "px " + $i + "px " + $settings + if( $i != $stack, ", ", "" );
	}

	// Return complete shadow property
	#{$type}-shadow: unquote( $value );
}



/**
