@mixin parallaxWrap()
{
	// For this to work we need html and body to have specific properties, so jump back to root and set these
	@at-root
	{
		html
		{
			// Size
			height: 100%;
			// Design
			// overflow: hidden;
		}

		body
		{
			height: 100%;
			// overflow: hidden;
		}
	}

	overflow: hidden;
	position: relative;
}



/**
 * { item_description }
 */
@mixin parallaxItem( $translateZ: -0.5px, $scale: 1.5, $translateY: 0% )
{
	// For this to work we need html and body to have specific properties, so jump back to root and set these
	@at-root
	{
		html
		{
			height: 100%;
			// overflow: hidden;
		}

		body
		{
			// overflow: hidden;
		}
	}

	// Design
	transform: translateZ( $translateZ ) scale( $scale ) translateY( $translateY );
}



/**
 * Sets the vertical padding.
 */
@mixin sectionPaddingVertical( $padding: 26px )
{
	padding-bottom: $padding;
	padding-top: $padding;
}



/**
 * Sets the width for most sections.
 */
@mixin sectionWidth( $args: false )
{
	@if ( false == $args )
	{
		$args:
		(
		);
	}

	$args: map-merge(
		(
			'padding': 15px,
			'useIntermediaryMaxWidths': true,
		),
		$args
	);

	margin-left: auto;
	margin-right: auto;
	max-width: 1200px;
	padding-left: map-get( $args, 'padding' );
	padding-right: map-get( $args, 'padding' );
	width: 100%;

	@if true == map-get( $args, 'useIntermediaryMaxWidths' )
	{
		@include breakpoint( 1200px 992px )
		{
			max-width: 970px;
		}

		@include breakpoint( 991px 768px )
		{
			max-width: 750px;
		}
	}
}



/**
 * Sets the width for narrower sections.
 */
@mixin sectionWidthNarrow()
{
	margin-left: auto;
	margin-right: auto;
	padding-left: 15px;
	padding-right: 15px;
	width: 100%;

	@media ( min-width: 768px )
	{
		max-width: 750px;
		width: 100%;
	}

	@media ( min-width: 992px )
	{
		max-width: 970px;
	}

	@media ( min-width: 1200px )
	{
		max-width: 1200px;
	}

	// Shop pages: Product listing
	body.woocommerce.post-type-archive-product &
	{
		@include breakpoint( 480px )
		{
			padding-left: 30px;
			padding-right: 30px;
		}
	}
}



@mixin buttonColorFix( $append: '', $addTransitions: true ) {
	// Design
	background-color: unquote( map-get-deep( $colors, 'brand', 'red' ) + $append );

	@if $addTransitions == true {
		// Design
		transition: background-color map-get-deep( $transitions, 'base' );
	}

	&:hover {
		// Design
		background-color: unquote(
			darken(
				map-get-deep(
					$colors,
					'brand',
					'red'
				),
				10%
			)
			+ $append
		);
	}
}






/**
 * { item_description }
 *
 * @param      {<type>}  <parameter_name>  [<default>]  - { parameter_description }
 * @param      {<type>}  <parameter_name>               - { parameter_description }
 */
@mixin scaleImageOnHover( $scaling: 1.2, $transition: map-get-deep( $transitions, 'base' ) ) {

	&:hover {

		img {
			// Design
			transform: scale( $scaling ) !important;
		}
	}

	img {
		// Design
		transition: transform $transition !important;
	}
}





@mixin ajaxAddToCartButton {

	.add-to-cart-wrap {
		// Design
		opacity: 1;

		.added_to_cart,
		.ajax_add_to_cart {
			// Layout
			padding: 7px 10px;
			// Size
			min-width: 0;
			width: auto;
			height: 34px;
			// Design
			background: map-get-deep( $colors, 'brand', 'red' ) !important;
			opacity: 1;
			// Text
			color: #fff;
			font-size: 17px;
			line-height: 23px !important;
			letter-spacing: -0.04em;
			text-transform: uppercase;

			&:hover {
				// Size
				width: auto;
				// Design
				background: map-get-deep( $colors, 'brand', 'red' ) !important;
			}

			&::after {
				// Layout
				display: none !important;
			}
		}
	}
}





@mixin closePopupButton( $wh: 50px )
{
	.custom-popup__close-button,
	.popup-close,
	.popup-form__close-button
	{
		background-color: map-get-deep( $colors, 'brand', 'red' );
		border: none;
		font-size: 22px;
		height: $wh;
		line-height: $wh;
		padding: 0;
		right: 0;
		text-align: center;
		top: 0;
		transition: background-color map-get-deep( $transitions, 'base' );
		width: $wh;
		z-index: 800;

		&:hover
		{
			// Design
			background-color: darken( map-get-deep( $colors, 'brand', 'red' ), 10% );
		}
	}
}





@mixin linkStyling {

	a {
		// Text
		color: map-get-deep( $colors, 'brand', 'red' );
	}
}
