.footer
{
	$BEMBase: &;
	background-color: #F6F7F8;
	padding-top: 26px;

	&__bottom-link
	{
		font-family: map-get( $fonts, 'newBase' ), map-get( $fonts, 'fallback' );
		font-size: 14px;
	}

	&__bottom-link-item
	{
		display: inline-block;
		padding-right: 43px;

		&:last-child
		{
			padding-right: 0;
		}
	}

	&__bottom-links
	{
		list-style-type: none;
		margin: 0;
		padding: 30px 0 0 0;

		@media ( min-width: 1200px )
		{
			text-align: center;
		}
	}

	&__column
	{
		flex: 0 0 100%;
		width: 100%;
		margin-top: 22px;

		&:first-child
		{
			margin-top: 0;
		}

		@media ( min-width: 520px )
		{
			flex: 0 0 50%;
			width: 50%;

			&:nth-child( 2 )
			{
				margin-top: 0;
			}
		}

		@media ( min-width: 1200px )
		{
			flex: 0 0 25%;
			width: 25%;

			&:nth-child( 3 ),
			&:nth-child( 4 )
			{
				margin-top: 0;
			}
		}
	}

	&__columns
	{
		display: flex;
		flex-wrap: wrap;
	}

	&__columns-link
	{
		font-family: map-get( $fonts, 'newBase' ), map-get( $fonts, 'fallback' );
		color: map-get-deep( $colors, 'brand', 'red' );
		font-size: 20px;
		letter-spacing: 0.3px;
	}

	&__columns-link-item
	{
		& + &
		{
			padding-top: 17px;
		}
	}

	&__columns-links
	{
		list-style-type: none;
		padding: 0;
	}

	&__columns-title
	{
		font-family: map-get( $fonts, 'newBase' ), map-get( $fonts, 'fallback' );
		font-size: 24px;
		font-weight: bold;
		letter-spacing: 0.1px;
		margin: 28px 0 31px 0px;
	}

	&__copyright-info
	{
		font-family: map-get( $fonts, 'newBase' ), map-get( $fonts, 'fallback' );
		font-size: 24px;
		font-weight: bold;
		letter-spacing: 0.2px;
		padding-top: 30px;

		@media ( min-width: 1200px )
		{
			text-align: center;
		}
	}

	&__payment-gateway-info
	{
		margin-top: 60px;

		@media ( min-width: 1200px )
		{
			text-align: center;
		}
	}

	&__payment-gateway-info-image
	{
		max-width: 455px !important;
		width: 100% !important;
	}

	&__row
	{
		&--bottom
		{
			background-color: #fff;
			padding-bottom: 94px;
		}

		&--top
		{
			padding-bottom: 56px;
		}
	}

	&__row-wrap
	{
		@include sectionWidthNarrow();
	}
}