/*
 * Generic functionality not related to a specific piece of content.
 */



html
{
	&.overflow-hidden
	{
		overflow: hidden !important;
		position: relative;
	}
}

// Main body styling
body
{
	font-family: map-get( $fonts, 'base' ), map-get( $fonts, 'fallback' );
	background-color: map-get( $colors, 'pageBackground' ) !important;
	position: relative;
	padding-top: 148px;

	@media ( min-width: 992px )
	{
		padding-top: 0;
	}

	&.boxed
	{
		#yolo-wrapper
		{
			overflow: visible;
		}
	}

	&.overflow-hidden
	{
		overflow: hidden !important;
		position: relative;
	}

	&.menu-mobile-in
	{
		left: 0 !important;
	}

	#yolo-wrapper
	{
		margin: 0 !important;
		max-width: 100% !important;
		overflow: visible;
		padding: 0 !important;
	}

	h1
	{
		font-family: map-get-deep( $fonts, 'heading', 'alt2' ), map-get( $fonts, 'fallback' );
	}

	h2,
	h3,
	h4,
	h5,
	h6 {
		font-family: map-get-deep( $fonts, 'heading', 'alt1' ), map-get( $fonts, 'fallback' );
	}

	a {
		// Design
		text-decoration: none;
		transition: color map-get( $transitions, 'base' );

		&:hover {
			// Text
			color: map-get-deep( $colors, 'brand', 'red' );
		}

		&:focus {
			// Text
			color: map-get-deep( $colors, 'brand', 'red' );
		}

		&:active {
			// Text
			color: map-get-deep( $colors, 'brand', 'red' );
		}
	}

	.yolo-site-content-page {
		// Mixins
		@include linkStyling;
	}
}

// Main page background
#background-image {
	// Layout
	position: absolute;
	top: 0;
	right: 0;
	left: 0;
	z-index: -1;

	// body.error404 & {
	// 	// Layout
	// 	z-index: map-get-deep( $zIndex, '404', 'bgImg' );
	// }

	&::before,
	&::after {
		content: "";
		// Layout
		display: block;
		position: absolute;
		right: 0;
		left: 0;
		// Size
		width: 100%;
	}

	&::before {
		// Layout
		top: 0;
		// Size
		height: 30%;
		// Design
		background-image:
			linear-gradient( 0deg, transparent, map-get( $colors, 'pageBackground' ) );
	}

	&::after {
		// Layout
		bottom: 0;
		// Size
		height: 30%;
		// Design
		background-image:
			linear-gradient( 0deg, map-get( $colors, 'pageBackground' ), transparent );
	}

	img {
		// Layout
		display: block;
		// Size
		width: 100%;
		height: auto;
	}
}


// User-selected text
::selection {
	// Design
    background-color: map-get-deep( $colors, 'brand', 'red' ) !important;
    // Text
    color: #ffffff;
}


// Section formatting
[class^="s-"] {
	// Layout
	clear: both;
}


#perspective-wrapper {
	// Size
	height: 100%;
	// Design
	overflow-x: hidden;
	overflow-y: scroll;
	perspective: 1px;
	transform-style: preserve-3d;
}




#wpadminbar
{
	overflow: hidden;

	@include breakpoint( 600px 0 )
	{
		position: fixed !important;
		top: 0 !important;
	}
}

.hide {
	// Layout
	display: none;
}

.full-width-image
{
	// Size
	width: 100%;
}

.yolo-footer-wrapper
{
	overflow: hidden;
}

.generic-button
{
	@include buttonColorFix();
	border: none;
	color: #fff;
	font-size: 1.1em;
	padding: 5px 20px;
	transition: background-color .3s;

	&--mc-signup
	{
		padding-bottom: 8px;
		padding-top: 8px;
	}
}

.mc4wp-form
{
	text-align: center;

	input,
	label
	{
		@media ( max-width: 499px )
		{
			width: 100% !important;
		}
	}

	.generic-button
	{
		@media ( max-width: 499px )
		{
			width: 100% !important;
		}
	}
}

img
{
	&.lazyload-fade-in
	{
		opacity: 0;
		transition: opacity 0.5s;
	}

	&.lazyloaded
	{
		opacity: 1;
		transition: opacity 0.5s;
	}
}

.grecaptcha-badge
{
	z-index: 2;
}

.upsells.products
{
	.owl-nav
	{
		.owl-next,
		.owl-prev
		{
			position: absolute;
			top: 50%;

			.fa
			{
				font-size: 25px;

				@media ( max-width: 768px )
				{
					background: rgba( 0, 0, 0, 0.05 );
					font-size: 35px;
				}
			}
		}

		.owl-next
		{
			right: 0;

			.fa
			{
				@media ( max-width: 768px )
				{
					border-radius: 0 0 5px 5px;
					padding: 10px 10px 10px 15px;
				}
			}
		}

		.owl-prev
		{
			left: 0;

			.fa
			{
				@media ( max-width: 768px )
				{
					border-radius: 5px 5px 0 0;
					padding: 10px 15px 10px 10px;
				}
			}
		}
	}
}
