.category-link
{
	$BEMBase                        : &;
	$borderStyle                    : 1px solid #e4e4e4;
	$desktopContentHeightWithImage  : 50px;
	$mobileContentHeightWithImage   : 105px;
	$mobileContentHeightWithoutImage: 65px;
	$verticalPadding                : 19px;
	border-bottom: $borderStyle;
	display: block;
	height: $mobileContentHeightWithoutImage + ( $verticalPadding * 2 );
	margin-left: -15px;
	margin-right: -15px;
	padding-bottom: $verticalPadding;
	padding-left: 30px;
	padding-right: 30px;
	padding-top: $verticalPadding;

	&:first-of-type
	{
		border-top: $borderStyle;
	}

	@media ( min-width: 768px )
	{
		border-radius: 4px;
		border: $borderStyle;
		height: $desktopContentHeightWithImage + ( $verticalPadding * 2 );
		margin-left: 0;
		margin-right: 0;
		padding-bottom: $verticalPadding;
		padding-left: 15px;
		padding-right: 15px;
		padding-top: $verticalPadding;
		width: 49.5%;

		&:nth-of-type( 2n )
		{
			margin-left: 1%;
		}

		&:nth-of-type( 1n+3 )
		{
			margin-top: 1%;
		}
	}

	@media ( min-width: 992px )
	{
		width: 49%;

		&:nth-of-type( 2n )
		{
			margin-left: 2%;
		}

		&:nth-of-type( 1n+3 )
		{
			margin-top: 2%;
		}
	}

	&--has-image
	{
		height: $mobileContentHeightWithImage + ( $verticalPadding * 2 );

		@media ( min-width: 768px )
		{
			height: $desktopContentHeightWithImage + ( $verticalPadding * 2 );
		}
	}

	&__icon
	{
		height: 40px;
		width: 12px;
	}

	&__icon-wrap
	{
		flex: 0 0 20px;
		height: 40px;
		width: 20px;
	}

	&__image-wrap
	{
		border-radius: 50%;
		flex: 0 0 $mobileContentHeightWithImage;
		height: $mobileContentHeightWithImage;
		margin-right: 10px;
		overflow: hidden;
		width: $mobileContentHeightWithImage;

		@media ( min-width: 500px )
		{
			margin-right: 32px;
		}

		@media ( min-width: 768px )
		{
			flex: 0 0 $desktopContentHeightWithImage;
			height: $desktopContentHeightWithImage;
			margin-right: 15px;
			width: $desktopContentHeightWithImage;
		}
	}

	&__inner-wrap
	{
		align-items: center;
		display: flex;
		flex-wrap: nowrap;
		height: 100%;
		left: -10px;
		position: relative;

		@media ( min-width: 768px )
		{
			left: 0;
		}
	}

	&__title
	{
		font-family: map-get( $fonts, 'newBase' ), map-get( $fonts, 'fallback' );
		color: map-get-deep( $colors, 'brand', 'red' );
		flex: 1 1 auto;
		margin: 0;
		font-size: 20px;
		font-weight: bold;
	}
}
