.custom-popup
{
	@include closePopupButton( 40px );

	&__close-button
	{
		border-radius: 0 0 0 17px;
		color: #fff;
		position: absolute;
	}

	&__heading
	{
		margin-bottom: 30px;
		text-transform: uppercase;

		@include breakpoint( 'max-height' 768px )
		{
			margin-top: 0;
			margin-bottom: 10px;
		}

		span
		{
			color: map-get-deep( $colors, 'brand', 'red' );
			display: block;
			font-family: Raleway;
			font-weight: 300;
		}
	}
}
