.header
{
	&__left-area
	{
		display: block;
		left: map-get( $newLayout, 'sidePadding' );
		position: absolute;
		top: 50%;
		transform: translateY( -50% );

		.search-form
		{
			.search-field
			{
				background-color: #fff !important;
				border: 1px solid  #f1f1f1 !important;
			}
		}
	}

	&__service-booking-button
	{
		-webkit-appearance: none;
		background: none;
		border: 2px solid map-get-deep( $colors, 'brand', 'red' );
		color: map-get-deep( $colors, 'brand', 'red' );
		display: block;
		height: 45px;
		line-height: 41px;
		margin-top: 10px;
		padding-left: 7px;
		text-align: left;
		transition: background-color 0.3s;
		width: 100%;

		&:hover
		{
			background-color: rgba( map-get-deep( $colors, 'brand', 'red' ), 0.07 );
		}

		svg
		{
			fill: currentColor;
			height: 27px;
			margin-right: 11px;
			position: relative;
			top: -1px;
			vertical-align: middle;
			width: 27px;
		}
	}
}




body
{
	&.ari-noscroll
	{
		#yolo-header
		{
			display: none !important;
		}
	}

	// Mobile menu. Does not include header. (separate due to stacking context issues. Or rather: a weird CSS spec)
	.yolo-mobile-header-nav
	{
		$fontSize: 19px;
		$baseLinkSidePadding: 33px;
		$levelIndentation: 17px;
		background-color: #fff;
		display: none;

		@media print
		{
			display: none !important;
		}

		&.menu-drop-fly
		{
			border: none;
			left: - map-get( $newLayout, 'mobileMenuDynamicWidth' );
			width: map-get( $newLayout, 'mobileMenuDynamicWidth' );

			@media ( min-width: map-get( $newLayout, 'mobileMenuMaxWidthBreakpoint' ) )
			{
				left: - map-get( $newLayout, 'mobileMenuTargetWidth' );
				width: map-get( $newLayout, 'mobileMenuTargetWidth' );
			}

			ul
			{
				&.yolo-nav-mobile-menu
				{
					font-family: map-get( $fonts, 'newBase' ), map-get( $fonts, 'fallback' );
					margin-bottom: 0;

					li
					{
						&.menu-item
						{
							border-bottom: none !important;

							&.menu-item-has-children
							{
								& > a
								{
									color: #000;
									font-weight: bold;
								}
							}

							&.sub-menu-open
							{
								& > .menu-caret
								{
									transform: rotate( 180deg );
								}

								& > ul
								{
									&.sub-menu
									{
										&:before
										{
											display: none;
										}
									}
								}
							}

							&.level-1
							{
								& > a
								{
									font-size: 18px;
									letter-spacing: 0.3px;
									padding-left: ( $baseLinkSidePadding + ( 1 * $levelIndentation ) );
									padding-top: 21px;
								}
							}

							&.level-2
							{
								& > a
								{
									font-size: 18px;
									letter-spacing: 0.3px;
									padding-left: ( $baseLinkSidePadding + ( 2 * $levelIndentation ) );
									padding-top: 21px;
								}
							}


							&.level-3
							{
								& > a
								{
									font-size: 18px;
									letter-spacing: 0.3px;
									padding-left: ( $baseLinkSidePadding + ( 3 * $levelIndentation ) );
									padding-top: 21px;
								}
							}


							&.level-4
							{
								& > a
								{
									font-size: 18px;
									letter-spacing: 0.3px;
									padding-left: ( $baseLinkSidePadding + ( 4 * $levelIndentation ) );
									padding-top: 21px;
								}
							}

							& > a
							{
								border-bottom: 1px solid #e4e4e4;
								color: map-get-deep( $colors, 'brand', 'red' );
								display: block;
								font-size: $fontSize;
								font-weight: 300;
								text-transform: none;
								width: 100%;
								font-size: 20px;
								letter-spacing: 0.1px;
								padding: 22px 55px 19px $baseLinkSidePadding;
							}

							& > .menu-caret
							{
								background: none;
								border: none;
								padding-bottom: 24px;
								padding-left: 30px;
								padding-right: 30px;
								padding-top: 26px;
								position: absolute;
								right: 0;
								top: 0;
								transition: border 0.3s, color 0.3s, transform 0.18s;
								transform-origin: 50% 50%;

								svg
								{
									height: 14px;
									width: 22px;

									g
									{
										fill: #000;
									}
								}

								&:hover
								{
									svg
									{
										g
										{
											fill: #fff;
										}
									}
								}
							}

							&.current-menu-ancestor,
							&.current_page_ancestor,
							&.current-menu-parent,
							&.current-menu-item,
							&.menu-current,
							&:hover
							{
								background-color: map-get-deep( $colors, 'brand', 'red' ) !important;

								& > a
								{
									color: #fff !important;

									@include breakpoint( 991px )
									{
										color: #fff;
									}
								}

								& > .menu-caret
								{
									svg
									{
										g
										{
											fill: #fff;
										}
									}
								}
							}
						}

						ul.sub-menu
						{
							li
							{
								&.menu-item
								{
									background-color: #f9f9f9 !important;

									&.current-menu-ancestor,
									&.current_page_ancestor,
									&.current-menu-parent,
									&.current-menu-item,
									&.menu-current,
									&:hover
									{
										background-color: map-get-deep( $colors, 'brand', 'red' ) !important;

										&.menu-item-has-children
										{
											& > a
											{
												color: #fff !important;
											}
										}
									}

									&.menu-item-has-children
									{
										&:hover
										{
											& > a
											{
												color: #fff !important;
											}
										}

										& > a
										{
											color: #000 !important;
										}
									}

									& > a
									{
										color: map-get-deep( $colors, 'brand', 'red' ) !important;
										font-size: $fontSize;
									}

									&:hover
									{
										background-color: map-get-deep( $colors, 'brand', 'red' ) !important;

										& > a
										{
											color: #fff !important;
										}
									}
								}
							}
						}
					}
				}
			}
		}
	}



	// Mobile header. Does not include menu.
	header
	{
		&#yolo-mobile-header
		{
			@media print
			{
				display: none !important;
			}

			.yolo-mobile-header-inner
			{
				.icon-search-menu
				{
					&:hover
					{
						@include breakpoint( 991px 0 )
						{
							color: map-get-deep( $colors, 'brand', 'red' );
						}
					}
				}
			}

			.cart_list_wrapper
			{
				p
				{
					&.buttons
					{
						.button
						{
							&.checkout
							{
								background-color: map-get-deep( $colors, 'brand', 'red' );

								&:hover
								{
									background-color: darken( map-get-deep( $colors, 'brand', 'red' ), 10% );
								}
							}
						}
					}
				}

				li
				{

					.cart-right
					{
						& > a
						{
							&:hover
							{
								color: map-get-deep( $colors, 'brand', 'red' );
							}
						}
					}
				}
			}

			.shopping-cart-wrapper
			{
				.widget_shopping_cart_content
				{
					.widget_shopping_cart_icon
					{
						& > i
						{
							&.wicon
							{
								& + span
								{
									background-color: map-get-deep( $colors, 'brand', 'red' );

									@include breakpoint( 991px 0 )
									{
										background-color: map-get-deep( $colors, 'brand', 'red' );
									}
								}
							}
						}
					}
				}
			}

			.yolo-mobile-menu-overlay
			{
				background-color: rgba( 0, 0, 0, 0.9 );
			}
		}
	}



	// Desktop header and menu
	header
	{
		&.yolo-main-header
		{
			position: relative;
			z-index: map-get-deep( $zIndex, 'header', 'base' );

			*:not( .fa ):not( [class*="pe-7"] )
			{
				font-family: map-get( $fonts, 'newBase' ), map-get( $fonts, 'fallback' );
			}

			@media print
			{
				// Layout
				display: none !important;
			}

			.yolo-header-nav-above
			{
				@include boxedLayout();

				border-bottom: none !important;
				position: relative;

				// Logo
				.header-logo
				{
					height: 142px !important;
					position: relative !important;

					img
					{
						image-rendering: -webkit-optimize-contrast;
					}

					svg
					{
						$width: 82px;
						$aspectRatio: ( 1288 / 1043 );
						height: #{ $aspectRatio * $width } !important;
						width: $width !important;
					}
				}

				// Cart
				.shopping-cart-wrapper
				{
					display: block;
					position: absolute;
					right: 0;
					top: 50%;
					transform: translateY( -50% );
					z-index: map-get-deep( $zIndex, 'header', 'cart' );

					@media ( min-width: 992px )
					{
						right: map-get( $newLayout, 'sidePadding' );
					}

					.sub-total-text
					{
						padding-bottom: 10px;
						padding-left: 20px;
						padding-top: 10px;
						top: 35px;
					}

					.widget_shopping_cart_content
					{
						.widget_shopping_cart_icon
						{
							& > i
							{
								&.wicon
								{
									& + span
									{
										&.total
										{
											background-color: map-get-deep( $colors, 'brand', 'red' );
										}
									}
								}
							}
						}

						.cart_list_wrapper
						{
							p
							{
								&.buttons
								{
									.button
									{
										&.checkout
										{
											background-color: map-get-deep( $colors, 'brand', 'red' );

											&:hover
											{
												background-color: darken( map-get-deep( $colors, 'brand', 'red' ), 10% );
											}
										}
									}
								}
							}

							li
							{
								.cart-right
								{
									& > a
									{
										&:hover
										{
											color: map-get-deep( $colors, 'brand', 'red' );
										}
									}
								}
							}
						}
					}
				}
			}

			// Main menu wrapper
			#undefined-yolo-sticky-wrapper
			{
				// Size
				height: auto !important;
			}

			.yolo-header-wrapper
			{
				.menu-wrapper
				{
					display: flex;
					flex-direction: row;
					flex-wrap: wrap;

					@media ( min-width: 768px )
					{
						border-top: solid 1px #eee;
					}

					#main-menu
					{
						@include boxedLayout();
						display: flex;
						flex-wrap: nowrap;
						justify-content: space-between;
						position: relative;

						@media ( min-width: 992px )
						{
							padding-left: 0;
							padding-right: 0;
						}

						&.yolo-main-menu
						{
							margin-left: auto;
							margin-right: auto;

							& > li
							{
								&:first-child
								{
									& > a
									{
										padding-left: map-get( $newLayout, 'sidePadding' ) !important;
									}
								}

								&:last-child
								{
									& > a
									{
										padding-right: map-get( $newLayout, 'sidePadding' ) !important;
									}
								}

								& > a
								{
									@include breakpoint( 1199px 991px )
									{
										padding: 20px 10px;
									}
								}
							}

							li
							{
								font-family: map-get( $fonts, 'newBase' ), map-get( $fonts, 'fallback' );

								&:hover
								{
									& > a
									{
										color: map-get-deep( $colors, 'brand', 'red' ) !important;
									}
								}

								&.current-menu-item,
								&.current_page_ancestor,
								&.current-menu-ancestor,
								&.current-menu-parent
								{
									& > a
									{
										color: map-get-deep( $colors, 'brand', 'red' ) !important;
									}
								}

								&.menu-item-has-children
								{
									&:hover
									{
										& > a
										{
											color: map-get-deep( $colors, 'brand', 'red' ) !important;
										}
									}
								}

								&.menu_style_dropdown
								{
									.sub-menu
									{
										li
										{
											&:before
											{
												background: map-get-deep( $colors, 'brand', 'red' );
											}
										}
									}
								}

								&.menu_style_column
								{
									& > .sub-menu
									{
										& > li
										{
											& > a
											{
												&:after
												{
													background: map-get-deep( $colors, 'brand', 'red' );
												}
											}
										}
									}
								}

								&.yolo_megamenu
								{
									.sub-menu
									{
										.sub-menu
										{
											a
											{
												&::after
												{
													content: '';
												}
											}

											.sub-menu
											{
												display: none;
											}
										}
									}
								}

								a
								{
									font-family: map-get( $fonts, 'newBase' ), map-get( $fonts, 'fallback' );
								}
							}
						}
					}

					.menu-caret
					{
						display: none;
					}
				}
			}
		}
	}

	header,
	#yolo_search_popup_wrapper {

		.search-form {

			.brp-logo-searchbox {
				// Layout
				position: absolute;
				top: 6px;
				left: 5px;
				// Size
				height: 34px;

				@include breakpoint( 991px 0 ) {
					// Layout
					display: none;
				}
			}

			.search-field {

				@include breakpoint( 992px ) {
					// Layout
					padding-left: 50px;
				}
			}
		}

		.header-logo {

			a {
				// Layout
				display: inline-block;
				position: relative;

				img {
					// Layout
					position: static !important;

					&.brp-logo-center {
						// Layout
						position: absolute !important;
						left: 111%;
						left: calc(100% + 10px);
						top: 14.5%;
						// Size
						height: 50px;

						@include breakpoint( 992px ) {
							// Layout
							display: none;
						}
					}
				}
			}
		}
	}

	header
	{
		.basket-icon
		{
			width: 24px;
			height: 35px;
		}

		.shopping-cart-wrapper
		{
			height: 100%;

			&.with-price
			{
				.sub-total-text
				{
					span
					{
						&.amount
						{
							color: map-get-deep( $colors, 'brand', 'red' );
						}
					}
				}
			}

			.widget_shopping_cart_content
			{
				&:hover
				{
					.cart_list_wrapper
					{
						top: 50px;

						@media ( min-width: 992px )
						{
							top: 85px;
						}
					}
				}

				.cart_list_wrapper
				{
					top: 95px;

					@media ( min-width: 992px )
					{
						top: 120px;
					}

					li
					{
						.cart-right
						{
							& > a
							{
								&:hover
								{
									color: map-get-deep( $colors, 'brand', 'red' );
								}
							}
						}
					}

					p
					{
						&.buttons
						{
							display: flex;
							flex-wrap: nowrap;

							.button
							{
								&.checkout
								{
									@include buttonColorFix();
								}
							}
						}
					}
				}

				.widget_shopping_cart_icon
				{
					height: 35px;
					top: 17px;
					width: 24px;

					@media ( min-width: 992px )
					{
						top: 50px;
					}

					.total
					{
						border-radius: 0;
						color: map-get-deep( $colors, 'brand', 'red' );
						font-weight: bold;
						height: 20px;
						line-height: 20px;
						position: absolute;
						right: 2px;
						text-align: center;
						top: 11px;
						width: 20px;
					}
				}

				.sub-total-text.sub-total-text.sub-total-text
				{
					color: map-get-deep( $colors, 'brand', 'red' );
					font-size: 16px;
					font-weight: bold;
					padding-right: 15px;
					position: absolute;
					right: 22px;
					top: 51%;
					transform: translateY( -50% );

					@media ( min-width: 992px )
					{
						top: 70px;
					}
				}
			}
		}
	}

	.below-mobile-menu
	{
		font-family: map-get( $fonts, 'newBase' ), map-get( $fonts, 'fallback' );
		font-size: 20px;
		letter-spacing: 0.2px;
		padding: 24px 33px;

		a
		{
			color: map-get-deep( $colors, 'brand', 'red' );
		}

		p
		{
			margin: 0 0 24px 0;
		}
	}
}
