.popup-form
{
	$BEMBase: &;
	bottom: 0;
	min-height: 100%;
	left: 0;
	opacity: 0;
	overflow: auto;
	position: fixed;
	right: 0;
	text-align: center;
	top: 0;
	transition: opacity 0.3s, visibility 0.3s;
	visibility: hidden;
	width: 100%;
	z-index: map-get-deep( $zIndex, 'card', 'popup' );

	&--active
	{
		opacity: 1;
		visibility: visible;

		#{ $BEMBase }
		{
			&__wrap
			{
				animation-duration: map-get-deep( $transitions, 'base' );
				animation-name: slideDown;
			}
		}
	}

	&__inner-wrap
	{
		box-shadow: $popupBoxShadow;
		padding: 15px 50px;

		@include breakpoint( 520px 0 )
		{
			padding: 15px 25px;
		}
	}

	&__outer-wrap
	{
		min-height: 100%;
		padding-bottom: 15vh;
		padding-top: 15vh;
		position: relative;
		width: 100%;
	}

	&__overlay
	{
		background-color: $popupOverlayBackgroundColor;
		bottom: 0;
		cursor: pointer;
		display: block;
		height: 100%;
		left: 0;
		position: absolute;
		right: 0;
		top: 0;
		width: 100%;
		z-index: map-get-deep( $zIndex, 'card', 'popup' );
	}

	&__wrap
	{
		background-color: #fff;
		display: inline-block;
		margin: auto;
		max-width: 95%;
		position: relative;
		z-index: ( map-get-deep( $zIndex, 'card', 'popup' ) + 1 );

		.fa
		{
			left: 1px;
			position: relative;
			top: -1px;
		}

		.wpcf7-form
		{
			.wpcf7-recaptcha
			{
				margin-bottom: 10px;

				& > div
				{
					height: auto !important;
					width: 100% !important;
				}
			}

			& > p
			{
				@include breakpoint( 'max-height' 768px )
				{
					margin-bottom: 5px;
				}
			}
		}

		.required-asterisk
		{
			color: map-get-deep( $colors, 'brand', 'red' );
		}

		.wpcf7-form-control-wrap
		{
			display: block;

			input,
			select,
			textarea
			{
				max-width: 100%;
			}
		}

		.wpcf7-submit
		{
			background-color: map-get-deep( $colors, 'brand', 'red' );
			border: none;
			color: #fff;
			font-size: 16px;
			padding: 10px 25px;
		}
	}
}
