/**
 * Common styling for spare parts pages.
 */


.l-selling-points
{
	// Mixins
	@include layout( 12 fluid 0.25 );
	@include susy-clearfix;
	// Layout
	margin-top: 30px;
	margin-bottom: 15px;

	@include breakpoint( 960px 0 )
	{
		// Layout
		margin-top: 40px;
		margin-bottom: 25px;
	}

	@each $columns in ( 1, 2, 3, 4 )
	{
		&[data-columns="#{$columns}"]
		{
			.i-selling-points
			{
				// Mixins
				@include span( 12 / $columns );
				// Layout
				margin-bottom: 0;

				@include breakpoint( ( 350px * $columns ) 0 )
				{
					// Mixins
					@include span( 12 );
					@include last;
					// Layout
					margin-bottom: 8px;
				}

				&:nth-of-type( #{ $columns + 'n' } )
				{
					// Mixins
					@include last;
				}

				&:last-of-type
				{
					@include breakpoint( ( 350px * $columns ) 0 )
					{
						// Layout
						margin-bottom: 0;
					}
				}
			}
		}
	}

	.i-selling-points
	{
		// Layout
		margin-bottom: 8px;
		// Text
		color: map-get-deep( $colors, 'brand', 'red' );
		font-size: 19px;
		text-align: center;

		@include breakpoint( 960px 0 )
		{
			// Text
			font-size: 13px;
		}
	}
}


.page-template-page_spare-parts
{
	.page-title-margin
	{
		// Layout
		margin-bottom: 0;
	}

	.yolo-site-content-page
	{
		// Layout
		padding-bottom: 40px;
		// Design
		background-color: #f7f7f7;
	}

	.s-cards
	{
		// Layout
		padding-top: 80px;
	}

	.brand-heading
	{
		// Layout
		margin-top: 0;
		margin-bottom: 30px;
		padding-top: 20px;
		padding-right: 100px;
		padding-bottom: 20px;
		position: relative;
		// Design
		border-bottom: 2px solid #333;
		// Text
		font-size: 19px;
		font-weight: 700;

		.logo
		{
			// Layout
			position: absolute;
			right: 0;
			top: 50%;
			// Size
			width: 100px;
			height: auto;
			// Design
			transform: translateY( -50% );
		}
	}

	.woocommerce-message
	{
		background: #fff;
	}

	.partstream-wrap
	{
		// Layout
		margin-top: 30px;

		&__tabs
		{
			// Layout
			display: flex;
		}

		&__tab
		{
			// Layout
			flex: 1 0 auto;
			padding: 7px;
			position: relative;
			// Size
			width: 20%;
			// Design
			background: none;
			border: none;

			&--active
			{
				// Design
				background: #fff;
				box-shadow: 0 0 1px 0 rgba( 0, 0, 0, 0.15 );

				&::after
				{
					content: "";
					// Layout
					display: block;
					position: absolute;
					bottom: -5px;
					right: 0;
					left: 0;
					z-index: 99;
					// Design
					background-color: #fff;
					// Size
					height: 5px;
					width: 100%;
				}
			}

			&-image
			{
				width: 90%;

				@media ( min-width: 768px )
				{
					width: 70%;
				}

				&--lynx
				{
					width: 80%;

					@media ( min-width: 768px )
					{
						width: 60%;
					}
				}

				&--sea-doo
				{
					width: 100%;

					@media ( min-width: 768px )
					{
						width: 80%;
					}
				}

				&--husqvarna
				{
					width: 80%;

					@media ( min-width: 500px )
					{
						width: 70%;
					}

					@media ( min-width: 768px )
					{
						width: 50%;
					}
				}
			}
		}

		&__content
		{
			padding: 0 5px;

			#ariPartStream
			{
				~ .partstream-wrap__loading-graphic
				{
					display: none;
				}

				.preselect-done
				{
					display: none !important;
				}

				.back-to-brand-page
				{
					// Layout
					display: inline-block;
					margin-left: 7px;
					padding: 5px 10px;
					// Design
					background-color: map-get-deep( $colors, 'brand', 'red' );
					// Text
					color: #fff;
				}

				#ariPartSelectPanel
				{
					& > .ariMainTitle
					{
						// Layout
						display: none;
					}
				}

				.ariDropDown
				{
					// Design
					background: none;
					border-bottom: 1px solid rgba( 0, 0, 0, 0.12 );

					& > a
					{
						// Layout
						padding: 18px 16px;
						// Text
						color: #464646;
						font-size: 18px;

						&::after
						{
							// Layout
							right: 24px;
							top: 50%;
							transform: translateY( -50% );
							border-top: 0.4rem solid #868686;
							border-left: 0.48rem solid transparent;
							border-right: 0.48rem solid transparent;
							// Text
							font-size: 0;
						}
					}

					.jl_item_folder,
					.jl_item_assembly
					{
						// Layout
						display: none !important;
					}

					ul
					{
						// Design
						border: none;
						box-shadow: 0 2px 5px 0 rgba( 0, 0, 0, 0.16 ), 0 2px 10px 0 rgba( 0, 0, 0, 0.12 );

						li
						{
							// Layout
							padding: 18px 16px;
							// Design
							transition: background-color 250ms ease-in-out, color 250ms ease-in-out;
							// Text
							color: #464646;
							font-size: 18px;

							&:hover
							{
								// Design
								background-color: #f7f7f7;
								// Text
								color: #666;
							}

							&[rel="0"]
							{
								// Layout
								display: none;
							}

							ins.jl_item_folder
							{
								// Layout
								display: none;
							}
						}
					}
				}

				#ariInfoBack
				{
					// Text
					white-space: nowrap;
				}

				#ariparts_notice
				{
					// Layout
					margin: 15px 0 20px 0;
				}

				#ariAssemblyLabel
				{
					// Layout
					display: none !important;
				}

				#ariAssemblyInfo
				{
					// Layout
					margin-top: 40px;
				}
			}
		}
	}
}

