/*
 * Slideshow-related styling.
 */

.s-slideshow {

	.slideshow {
		// Layout
		position: relative;

		&:hover {

			.pager-button {
				// Design
				opacity: 0.8;
			}
		}

		.slide {
			// Layout
			position: relative;
			padding-bottom: 50%;
			// Size
			width: 100%;
			// Design
			overflow: hidden;

			&.cycle-slide-active {

				.slideshow-image-holder {
					// Design
					transform: scale( 1.2, 1.2 );
					transition: transform 11s;
					transition-delay: 0s;
				}
			}

			.slideshow-image-holder {
				// Layout
				position: absolute;
				// Size
				width: 100%;
				height: 100%;
				top: 0;
				right: 0;
				bottom: 0;
				left: 0;
				// Design
				background-size: cover;
				background-position: 50% 50%;
				transform: scale( 1, 1 );
				transition: transform 0s;
				transition-delay: 1s;
			}

			.text-wrap {
				// Layout
				display: flex;
				flex-direction: column;
				flex-wrap: nowrap;
				position: absolute;
				top: 0;
				bottom: 0;
				left: 50%;
				// Size
				width: auto;
				height: 100%;
				// Design
				transform: translateX( -50% );

				@include breakpoint( 1100px 965px ) {
					// Size
					width: 65%;
				}

				@include breakpoint( 964px 591px ) {
					// Size
					width: 75%;
				}

				@include breakpoint( 590px 521px ) {
					// Size
					width: 85%;
				}

				@include breakpoint( 520px 421px ) {
					// Size
					width: 90%;
				}

				@include breakpoint( 520px 0 ) {
					// Size
					width: 95%;
				}

				.icons {
					// Layout
					margin-top: auto;
					// Text
					text-align: center;

					& + .heading {
						// Layout
						margin-top: 0;
					}

					.icon,
					.fa {
						// Layout
						position: relative;
						// Text
						color: #fff;
						text-shadow: 1.5px 2px 3px rgba(30,30,30,0.6);


						&:first-of-type,
						&:last-of-type {
							// Text
							font-size: 9px;
						}

						&:nth-of-type( 2 ),
						&:nth-of-type( 4 ) {
							// Layout
							padding: 0 5px;
							top: -8px;
							// Text
							font-size: 13px;
						}

						&:nth-of-type( 3 ) {
							// Layout
							top: -14px;
							// Text
							font-size: 20px;
						}
					}
				}

				.heading {
					// Layout
					margin-top: auto;
					margin-bottom: 0;
					padding: 0.1em 0 0.05em 0;
					position: relative;
					// Text
					color: #fff;
					font-family: 'Raleway';
					font-size: 50px;
					font-weight: bold;
					text-align: center;
					text-shadow: 2.5px 3px 5px rgba(30,30,30,0.6);
					text-transform: uppercase;

					@include breakpoint( 768px 481px ) {
						// Text
						font-size: 35px;
					}

					@include breakpoint( 480px 421px ) {
						// Text
						font-size: 30px;
					}

					@include breakpoint( 480px 0 ) {
						// Text
						text-shadow: 1.5px 2px 3px rgba(30,30,30,0.6);
					}

					@include breakpoint( 420px 0 ) {
						// Text
						font-size: 25px;
					}

					&::before,
					&::after {
						content: '';
						// Layout
						display: block;
						position: absolute;
						right: 0;
						left: 0;
						// Size
						width: 100%;
						height: 2px;
						// Design
						background-color: #fff;
						box-shadow: 1.5px 2px 3px rgba(30,30,30,0.6);
					}

					&::before {
						// Layout
						top: 0;
					}

					&::after {
						// Layout
						bottom: 0;
					}
				}

				.subheading {
					// Layout
					margin-top: 0;
					margin-bottom: auto;
					padding-top: 0.5em;
					// Text
					color: #fff;
					text-align: center;
					text-shadow: 1.5px 2px 3px rgba(30,30,30,0.7);
					text-transform: uppercase;

					@include breakpoint( 420px 0 ) {
						// Text
						font-size: 20px;
					}
				}
			}
		}

		.pager-button {
			// Layout
			padding: 0;
			position: absolute;
			top: 50%;
			z-index: map-get-deep( $zIndex, 'slideshow', 'buttons' );
			// Size
			width: 70px;
			height: 70px;
			// Design
			background-color: rgb(31, 31, 31);
			border: none;
			border-radius: 50%;
			opacity: 0;
			transform: translateY( -50% );
			transition: opacity map-get( $transitions, 'base' );
			// Text
			color: #fff;
			font-size: 40px;
			line-height: 70px;

			@include breakpoint( 768px 0 ) {
				// Size
				width: 40px;
				height: 40px;
				// Text
				font-size: 30px;
				line-height: 40px;
			}

			&.next {
				// Layout
				right: 25px;

				@include breakpoint( 768px 0 ) {
					// Layout
					right: 15px;
				}

				.icon,
				.fa {
					// Layout
					right: -3px;

					@include breakpoint( 768px 0 ) {
						// Layout
						right: -2px;
					}
				}
			}

			&.prev {
				// Layout
				left: 25px;

				@include breakpoint( 768px 0 ) {
					// Layout
					left: 15px;
				}

				.icon,
				.fa {
					// Layout
					left: -3px;

					@include breakpoint( 768px 0 ) {
						// Layout
						left: -2px;
					}
				}
			}

			.icon,
			.fa {
				// Layout
				position: relative;
				top: -1px;
			}
		}
	}
}
