.husqvarna-xepc-wrap
{
	@include sectionWidth();
	margin-top: 20px;

	&__iframe
	{
		border: none;
		height: 2000px;
		width: 100%;

		&--non-interactive
		{
			opacity: 0.9999999;
			pointer-events: none;
			position: relative;
			touch-action: none;
			transform: translateY( 0.1px );
			z-index: -1;
		}
	}
}

.husqvarna-xepc-popup
{
	$bemBase: &;
	bottom: 0;
	display: table;
	height: 100%;
	height: 100%;
	left: 0;
	opacity: 0;
	overflow: auto;
	position: fixed;
	right: 0;
	top: 0;
	transition: opacity 0.3s, visibility 0.3s;
	visibility: hidden;
	width: 100%;
	width: 100%;
	z-index: 997;

	&--active
	{
		opacity: 1;
		visibility: visible;

		#{ $bemBase }
		{
			&__content
			{
				animation-duration: map-get-deep( $transitions, 'base' );
				animation-name: slideDown;
			}
		}
	}

	&__content
	{
		@include sectionWidth();
		background-color: #fff;
		box-shadow: $popupBoxShadow;
		display: inline-block;
		margin: 0 auto;
		max-width: 465px !important;
		padding: 15px 50px 40px 50px;
		position: relative;
		width: auto !important;
		z-index: 999;

		@media ( max-height: 768px )
		{
			padding-bottom: 15px;
		}
	}

	&__loading
	{
		display: none;
		margin-top: 15px;

		&--active
		{
			display: block;
		}

		.fa
		{
			font-size: 40px;
		}
	}

	&__loading-text
	{
		display: block;
		margin-top: 10px;
	}

	&__overlay
	{
		background-color: $popupOverlayBackgroundColor;
		bottom: 0;
		cursor: pointer;
		height: 100%;
		left: 0;
		position: absolute;
		right: 0;
		top: 0;
		width: 100%;
		z-index: 998;
	}

	&__price-unavailable
	{
		display: none;

		&--active
		{
			display: block;
		}

		a
		{
			color: map-get-deep( $colors, 'brand', 'red' );
			font-weight: bold;
		}

		p
		{
			margin-bottom: 0;
		}
	}

	&__product-info
	{
		display: none;

		&--active
		{
			display: block;
		}
	}

	&__price-wrapper
	{
		text-align: left;
	}

	&__product-title-wrapper
	{
		text-align: left;
	}

	&__quantity
	{
		display: flex;
		text-align: left;
	}

	&__sku-wrapper
	{
		text-align: left;
	}

	&__wrapper
	{
		display: table-cell;
		text-align: center;
		vertical-align: middle;
	}

	label
	{
		text-transform: uppercase;
		width: 135px !important;
	}
}

body.husqvarna-xepc-popup-shown
{
	overflow: hidden;

	.mobile-header__sticky-wrap
	{
		z-index: 30 !important;
	}

	#yolo-header
	{
		z-index: 20 !important;
	}
}
