/*
 * Facebook feed related styling.
 */


.s-facebook-feed {
	// Layout
	padding: 30px 15px;
	// Design
	background-color: #f6f6f6;

	.facebook-feed {
		// Design
		opacity: 0;
		transition: opacity map-get-deep( $transitions, 'base' );

		&.loaded {
			// Layout
			opacity: 1;
		}

		.item {
			// Layout
			margin: 0 auto 30px auto;
			padding: 15px 25px 20px 25px;
			// Size
			max-width: 800px;
			// Design
			background-color: #fff;
			box-shadow: 0px 1px 1px 1px #eeeeee;
			// Text
			text-align: center;

			.published-date {
				// Layout
				margin-bottom: 20px;
				// Design
				border-bottom: 2px solid map-get-deep( $colors, 'brand', 'red' );
				// Text
				font-size: 17px;
				font-weight: bold;
			}

			.img {
				// Layout
				display: inline-block;
				margin-bottom: 20px;

				img {
					// Mixins
					@include materialBoxShadow( 1 );

					// Size
					max-width: 500px;
					width: 100%;
				}
			}

			.message {
				// Text
				word-break: break-word;
			}

			.shared-link {
				// Mixins
				@include materialBoxShadow( 1 );

				// Layout
				display: inline-block;
				margin-top: 10px;
				padding: 7px 12px;
				// Design
				background-color: rgba( 50, 50, 50, 0.01 );
			}

			.post-link {
				// Layout
				display: block;
				margin-top: 15px;
				// Design
				opacity: 0.5;
				transition: opacity map-get-deep( $transitions, 'base' );
				// Text
				font-size: 0.9em;

				&:hover {
					// Design
					opacity: 1;
				}
			}
		}
	}
}
