.custom-breadcrumb
{
	.product-template-default &,
	.page-template-page_spare-parts:not( .page-child ) &,
	.post-type-archive-product &
	{
		@include sectionWidth(
			(
				'useIntermediaryMaxWidths': false,
			)
		);
		position: relative;
		z-index: map-get-deep( $zIndex, 'customBreadcrumb', 'mainElement' );
	}

	.product-template-default &
	{
		margin-top: -30px !important;

		@media ( max-width: 991px ) and ( min-width: 768px )
		{
			width: 750px !important;
		}

		@media ( max-width: 1199px ) and ( min-width: 992px )
		{
			width: 970px !important;
		}
	}

	.woocommerce-breadcrumb
	{
		font-family: map-get( $fonts, 'newBase' ), map-get( $fonts, 'fallback' );
		color: #000 !important;
		font-size: 12px !important;
		padding-left: 4px !important;
		padding-top: 15px !important;

		.page-template-page_spare-parts:not( .page-child ) &,
		.post-type-archive-product &
		{
			color: #fff !important;
			left: inherit;
			position: absolute;
			top: 0;
		}

		a
		{
			color: map-get-deep( $colors, 'brand', 'red' ) !important;

			.page-template-page_spare-parts:not( .page-child ) &,
			.post-type-archive-product &
			{
				color: #fff !important;
			}
		}

		.breadcrumb-delimiter
		{
			padding: 0 6px !important;
		}
	}
}
