.category-information
{
	$BEMBase: &;
	$mobileContentHeight : 105px;
	$desktopContentHeight: $mobileContentHeight;
	font-family: map-get( $fonts, 'newBase' ), map-get( $fonts, 'fallback' );
	@include sectionWidth(
		(
			'useIntermediaryMaxWidths': false,
		)
	);
	padding-bottom: 0;
	position: relative;
	z-index: map-get-deep( $zIndex, 'category', 'information' );

	@media ( min-width: 768px )
	{
		padding-top: 15px;
	}

	&__description
	{
    	margin-top: 18px;
    	padding-bottom: 10px;
		padding-right: 25px;

		&,
		& *
		{
			font-family: map-get( $fonts, 'newBase' ), map-get( $fonts, 'fallback' );
			font-size: 18px;
			font-weight: 500;
			line-height: 1.2;
		}

		*:last-child
		{
			margin-bottom: 0;
		}
	}

	&__image-wrap
	{
		border-radius: 50%;
		flex: 0 0 $mobileContentHeight;
		height: $mobileContentHeight;
		margin-right: 10px;
		overflow: hidden;
		width: $mobileContentHeight;

		@media ( min-width: 500px )
		{
			margin-right: 21px;
		}

		@media ( min-width: 768px )
		{
			flex: 0 0 $desktopContentHeight;
			height: $desktopContentHeight;
			width: $desktopContentHeight;
		}
	}

	&__inner-wrap
	{
		align-items: center;
		display: flex;
		flex-wrap: nowrap;
		padding-left: 5px;
	}

	&__text-wrap
	{
		flex: 1 1 auto;
	}

	&__title
	{
		font-family: map-get( $fonts, 'newBase' ), map-get( $fonts, 'fallback' );
		margin: 15px 0 0 0;
		font-size: 20px;
		font-weight: bold;

		&--bottom-margin
		{
			margin-bottom: 18px;
		}
	}
}