.generic-form
{
	text-align: center;

	&__main-heading
	{
		margin-bottom: 0;
		text-transform: uppercase;
	}

	&__sub-heading
	{
		color: map-get-deep( $colors, 'brand', 'red' );
		margin-top: 0;
		text-transform: uppercase;
	}

	.required-asterisk
	{
		color: map-get-deep( $colors, 'brand', 'red' );
	}

	.wpcf7
	{
		display: block;
		margin-left: auto;
		margin-right: auto;
		max-width: 360px;
		width: 100%;

		input,
		textarea
		{
			max-width: 100%;
		}
	}

	.wpcf7-form-control-wrap
	{
		display: block;
	}

	.wpcf7-recaptcha
	{
		display: inline-block;
		padding-left: 5px;
	}

	.wpcf7-submit
	{
		@include buttonColorFix();
		margin-top: 8px;
	}

	label
	{
		margin-bottom: 5px;
	}

	p
	{
		margin-bottom: 5px;
	}
}
