@mixin genericTextStyling()
{
	h2
	{
		font-family: map-get( $fonts, 'newBase' ), map-get( $fonts, 'fallback' );
		font-size: 24px;
		font-weight: bold;
		letter-spacing: 0.1px;
		margin: 20px 0;
	}

	h3
	{
		font-family: map-get( $fonts, 'newBase' ), map-get( $fonts, 'fallback' );
		font-size: 22px;
		font-weight: bold;
		letter-spacing: 0.3px;
		margin: 15px 0;
	}

	p,
	ul,
	ol
	{
		font-family: map-get( $fonts, 'newBase' ), map-get( $fonts, 'fallback' );
		font-size: 20px;
		hyphens: auto;
		letter-spacing: 0.3px;
		line-height: 1.62;
		word-break: break-word;
	}

	a
	{
		color: map-get-deep( $colors, 'brand', 'red' );

		&:hover
		{
			color: lighten( map-get-deep( $colors, 'brand', 'red' ), 15% );
		}
	}
}
