.hero
{
	$BEMBase           : &;
	$largeBreakpoint1  : 850px;
	$largeBreakpoint2  : 1000px;
	$largeScale1       : 1.3;
	$largeScale2       : 1.6;
	$mediumMaxHeight   : 486px;
	$tallMaxHeight     : 600px;
	$mediumMinHeight   : 250px;
	$tallMinHeight     : 250px;
	$smallerBreakpoint : 450px;
	$smallerScale      : 0.7;
	$mediumTargetHeight: 486px;
	$mediumTargetWidth : 772px;
	$tallTargetHeight  : 682px;
	$tallTargetWidth   : 1125px;

	overflow: hidden;
	position: relative;

	&--low
	{
		background-position: center center;
		background-size: cover;
	}

	&--medium,
	&--tall
	{
		height: 0;
		padding-bottom: percentage( $tallTargetHeight / $tallTargetWidth );
	}

	&--medium
	{
		@media ( max-width: ( $tallMinHeight / ( $tallTargetHeight / $tallTargetWidth ) ) )
		{
			height: $mediumMinHeight;
			padding-bottom: 0;
		}

		@media ( min-width: ( $mediumMaxHeight / ( $tallTargetHeight / $tallTargetWidth ) ) )
		{
			height: $mediumMaxHeight;
			padding-bottom: 0;
		}
	}

	&--tall
	{
		@media ( max-width: ( $tallMinHeight / ( $tallTargetHeight / $tallTargetWidth ) ) )
		{
			height: $tallMinHeight;
			padding-bottom: 0;
		}

		@media ( min-width: ( $tallMaxHeight / ( $tallTargetHeight / $tallTargetWidth ) ) )
		{
			height: $tallMaxHeight;
			padding-bottom: 0;
		}
	}

	&__button
	{
		font-family: map-get( $fonts, 'newBase' ), map-get( $fonts, 'fallback' );
		@include buttonColorFix();
		$fontSize     : 19px;
		$letterSpacing: 0.2px;
		$marginBottom : 16px;
		color: #fff;
		font-size: $fontSize;
		font-weight: bold;
		letter-spacing: $letterSpacing;
		max-width: 90%;
		margin-bottom: $marginBottom;
		padding: 10px 1rem 12px;
		text-align: center;
		width: 452px;

		@media ( max-width: $smallerBreakpoint )
		{
			font-size: ( $fontSize * $smallerScale );
			letter-spacing: ( $letterSpacing * $smallerScale );
			margin-bottom: ( $marginBottom * $smallerScale );
		}

		@media ( min-width: $largeBreakpoint1 )
		{
			font-size: ( $fontSize * $largeScale1 );
			letter-spacing: ( $letterSpacing * $largeScale1 );
			margin-bottom: ( $marginBottom * $largeScale1 );
		}
	}

	&__contents
	{
		&--low
		{
			padding: 50px 0;
			position: relative;
			text-align: center;
			z-index: 99;
		}

		&--medium,
		&--tall
		{
			align-items: center;
			bottom: 0;
			display: flex;
			flex-direction: column;
			height: 100%;
			justify-content: center;
			left: 0;
			position: absolute;
			right: 0;
			text-align: center;
			top: 0;
			width: 100%;
			z-index: 3;
		}
	}

	&__image
	{
		min-height: 100% !important;
		left: 50%;
		position: absolute;
		top: 50%;
		transform: translate( -50%, -50% );
		min-width: 100%;
		width: 100%;
		z-index: -1;

		&--medium
		{
			@media ( max-width: ( $mediumMinHeight / ( $mediumTargetHeight / $mediumTargetWidth ) ) )
			{
				height: 100% !important;
				max-width: none !important;
				width: auto;
			}
		}

		&--tall
		{
			@media ( max-width: ( $tallMinHeight / ( $tallTargetHeight / $tallTargetWidth ) ) )
			{
				height: 100% !important;
				max-width: none !important;
				width: auto;
			}
		}
	}

	&__text
	{
		font-family: map-get( $fonts, 'newBase' ), map-get( $fonts, 'fallback' );
		$fontSize: 24px;
		$letterSpacing: 0.3px;
		$marginBottom: 14px;
		color: #fff;
		font-size: $fontSize;
		letter-spacing: $letterSpacing;
		line-height: 1.7;
		margin-bottom: $marginBottom;
		margin-top: 2px;
		padding: 0 15px;

		&--low
		{
			margin-bottom: 0;
			margin-top: 0;
		}

		&--tall
		{
			@media ( max-width: $smallerBreakpoint )
			{
				font-size: ( $fontSize * $smallerScale );
				letter-spacing: ( $letterSpacing * $smallerScale );
				margin-bottom: ( $marginBottom * $smallerScale );
			}
		}

		&--medium,
		&--tall
		{
			@media ( min-width: $largeBreakpoint1 )
			{
				font-size: ( $fontSize * $largeScale1 );
				letter-spacing: ( $letterSpacing * $largeScale1 );
				margin-bottom: ( $marginBottom * $largeScale1 );
			}

			@media ( min-width: $largeBreakpoint2 )
			{
				font-size: ( $fontSize * $largeScale2 );
				letter-spacing: ( $letterSpacing * $largeScale2 );
				margin-bottom: ( $marginBottom * $largeScale2 );
			}
		}
	}

	&__title
	{
		font-family: map-get( $fonts, 'newBase' ), map-get( $fonts, 'fallback' );
		$fontSize: 34px;
		$letterSpacing: 0.4px;
		$marginBottom: 20px;
		color: #fff;
		font-size: $fontSize;
		font-weight: 600;
		letter-spacing: $letterSpacing;
		margin-bottom: $marginBottom;
		margin-top: 3px;
		padding: 0 15px;

		&--low
		{
			margin-bottom: 0;
			margin-top: 0;
		}

		// font-size: 35px;
		// padding-right: 2px;
		// letter-spacing: -0.1px;

		&--medium
		{
			font-size: 24px;
			font-weight: bold;
		}

		&--tall
		{
			@media ( max-width: $smallerBreakpoint )
			{
				margin-bottom: ( $marginBottom * ( $smallerScale * $smallerScale ) );
			}
		}

		&--tall
		{
			@media ( max-width: $smallerBreakpoint )
			{
				font-size: ( $fontSize * $smallerScale );
				letter-spacing: ( $letterSpacing * $smallerScale );
			}
		}

		&--medium
		{
			@media ( min-width: 608px )
			{
				font-size: ( $fontSize * $largeScale1 );
				letter-spacing: ( $letterSpacing * $largeScale1 );
				margin-bottom: ( $marginBottom * $largeScale1 );
			}
		}

		&--tall
		{
			@media ( min-width: $largeBreakpoint1 )
			{
				font-size: ( $fontSize * $largeScale1 );
				letter-spacing: ( $letterSpacing * $largeScale1 );
				margin-bottom: ( $marginBottom * $largeScale1 );
			}
		}

		&--medium,
		&--tall
		{
			@media ( min-width: $largeBreakpoint2 )
			{
				font-size: ( $fontSize * $largeScale2 );
				letter-spacing: ( $letterSpacing * $largeScale2 );
				margin-bottom: ( $marginBottom * $largeScale2 );
			}
		}
	}

	&__wrapper
	{
		&--darken
		{
			&::before
			{
				background: rgba( 0, 0, 0, 0.3 );
				content: '';
				display: block;
				width: 100%;
				height: 100%;
				position: absolute;
				bottom: 0;
				right: 0;
				left: 0;
				top: 0;
				z-index: 2;
			}
		}
	}
}
