/**
 * Styling for the link grid section.
 */


.link-grid
{
	// Localscoped variables
	$linkGridBreakpoints:
	(
		'row':    1001px,
		'column': 1000px 0,
	);
	$textShadow: 0 7px 65px #000;
	// Layout
	display: flex;
	flex-wrap: wrap;
	margin: auto;
	padding-top: 15px;
	// Size
	max-width: 1200px;

	.link-grid__item
	{
		// Layout
		display: block;
		padding: 15px;
		// Size
		width: 100%;
		min-height: 320px;
		// Design
		cursor: pointer;

		@include breakpoint( 640px )
		{
			// Size
			min-height: 400px;
		}

		@include breakpoint( 1280px )
		{
			// Size
			min-height: 480px;
		}

		@include breakpoint( map-get( $linkGridBreakpoints, 'row' ) )
		{
			// Size
			width: 60%;
		}

		// Alternating grid pattern
		&:nth-child( 4n + 2 ),
		&:nth-child( 4n + 3 )
		{
			@include breakpoint( map-get( $linkGridBreakpoints, 'row' ) )
			{
				// Size
				width: 40%;
			}
		}

		// Specific items set to show in 3 column layout
		// &:nth-child( 3 ),
		// &:nth-child( 4 ),
		// &:nth-child( 5 ),
		// &:nth-child( 6 ),
		// &:nth-child( 7 ),
		// &:nth-child( 8 )
		// {
		// 	@include breakpoint( map-get( $linkGridBreakpoints, 'row' ) )
		// 	{
		// 		// Size
		// 		width: percentage( 1 / 3 );
		// 	}
		// }

		.link-grid__item__wrap
		{
			// Layout
			display: flex;
			flex-direction: column;
			flex-wrap: wrap;
			justify-content: flex-end;
			padding-top: 30px;
			padding-right: 30px;
			padding-bottom: 30px;
			padding-left: 30px;
			// Size
			height: 100%;
			// Design
			background-color: #eee;
			background-size: cover;
			background-position: left top;
			transition: all 0.5s ease;

			&:hover
			{
				// Design
				transform: scale( 1.02 );
				transition-duration: 0.2s;
			}

			.link-grid__item__heading
			{
				font-family: map-get-deep( $fonts, 'heading', 'alt1' ), map-get( $fonts, 'fallback' );
				// Layout
				margin: 0;
				// Text
				color: #fff;
				font-size: 22px;
				font-weight: 600;
				text-shadow: $textShadow, $textShadow;

				@include breakpoint( 481px )
				{
					// Text
					font-size: 38px;
					text-shadow: $textShadow;
				}
			}

			.link-grid__item__text
			{
				// Layout
				margin: 10px 0 30px 0;
				// Text
				color: #fff;
				font-size: 17px;
				font-weight: 600;
				line-height: 1.4;
				text-shadow: $textShadow, $textShadow;

				@include breakpoint( 481px )
				{
					// Text
					font-size: 22px;
				}
			}

			.link-grid__item__button
			{
				// Layout
				display: inline-block;
				padding: 1rem 2rem;
				// Size
				max-width: 210px;
				// Design
				background: #780000;
				// Text
				color: #fff;
				font-size: 22px;
				text-align: center;
			}
		}
	}
}
