/**
 * Styling for the fullpage search feature accesible from the mobile menu.
 */


#yolo_search_popup_wrapper {

	.search-form {
		// Layout
		position: absolute;
		top: 40%;
		left: 10%;
		// Size
		width: 80%;

		::-webkit-input-placeholder {
			// Text
			color: #fff;
		}

		::-moz-placeholder {
			// Text
			color: #fff;
		}

		:-ms-input-placeholder {
			// Text
			color: #fff;
		}

		:-moz-placeholder {
			// Text
			color: #fff;
		}

		::placeholder {
			// Text
			color: #fff;
		}

		input {
			// Layout
			display: block;
			padding-right: 10px;
			padding-left: 10px;
			// Size
			width: 100%;
			// Design
			background: none;
			border-bottom: 2px solid  #fff;
			// Text
			color: #fff;
			font-size: 25px;
		}

		.icon,
		.fa {
			// Text
			color: #fff;
			font-size: 25px;
		}
	}
}
