/*
 * Cards-related styling.
 */


.s-cards {
	// Susy settings
	@include layout( 12 fluid 0.25 );
	@include susy-clearfix;

	// Mixins
	@include sectionWidth;

	// Layout
	display: flex;
	flex-wrap: wrap;
	flex-direction: row;
	position: relative;

	// If it's on the frontpage
	body.home & {
		// Layout
		margin-bottom: 30px;

		@include breakpoint( 992px ) {

			// Layout
			margin-top: -100px;
		}
	}

	body.post-type-archive-product &,
	body.archive & {
		// Layout
		padding-top: 90px;
	}

	body.single-product & {
		// Layout
		padding-top: 55px;
	}

	.card {
		// Layout
		padding: 20px;
		position: relative;
		z-index: map-get-deep( $zIndex, 'card', 'card' );
		// Design
		background-color: #f3f4f4;
		background-size: auto 100%;
		background-position: 100% 50%;
		background-repeat: no-repeat;
		border: none;
		overflow: hidden;
		transition: background-size 0.3s;
		// Text
		text-align: left;

		@include breakpoint( 768px ) {
			// Susy settings
			@include span( 4 );
		}

		@include breakpoint( 767px 0 ) {
			// Susy settings
			@include span( 12 );
			@include last;

			// Layout
			margin-bottom: 20px;
		}

		&:hover {

			.heading {
				// Text
				color: #333;
			}

			.ingress {
				// Text
				color: #000;
			}

			.button {
				// Text
				color: #fff;
			}

			img {
				// Size
				width: 75%;
			}
		}

		&:first-child {

			@include breakpoint( 767px 0 ) {
				// Layout
				margin-top: 10px;
			}
		}

		&:last-child {

			@include breakpoint( 768px ) {
				// Susy settings
				@include last;
			}
		}

		.text {
			// Layout
			max-width: 70%;

			@include breakpoint( 767px 0 ) {
				// Layout
				max-width: 60%;
			}

			.heading {
				// Layout
				margin: 0 0 3px 0;
				position: relative;
				z-index: map-get-deep( $zIndex, 'card', 'text' );
				// Text
				font-family: 'Raleway';
				font-weight: 600;
				line-height: 1.4;
				text-transform: uppercase;
				white-space: nowrap;

				@include breakpoint( 992px ) {
					// Text
					font-size: 27px;
				}

				@include breakpoint( 992px 768px ) {
					// Text
					font-size: 20px;
				}
			}

			.subheading {
				// Layout
				margin: -5px 0 10px 0;
				position: relative;
				z-index: map-get-deep( $zIndex, 'card', 'text' );
				// Text
				color: map-get-deep( $colors, 'brand', 'red' );
				font-family: 'Raleway';
				font-weight: 300;
				line-height: 1em;
				text-transform: uppercase;

				@include breakpoint( 992px ) {
					// Text
					font-size: 24px;
				}

				@include breakpoint( 992px 768px ) {
					// Text
					font-size: 17px;
				}
			}

			.ingress {
				// Localscoped variables
				$textShadow: 0 0 7px #f3f4f4;
				// Layout
				position: relative;
				z-index: map-get-deep( $zIndex, 'card', 'text' );
				// Text
				color: #000;
				font-size: 0.93em;
				text-shadow: $textShadow, $textShadow, $textShadow, $textShadow, $textShadow, $textShadow, $textShadow, $textShadow, $textShadow, $textShadow, $textShadow, $textShadow, $textShadow, $textShadow, $textShadow, $textShadow;
			}

			.link {
				// Mixins
				@include buttonColorFix();

				// Layout
				display: inline-block;
				position: relative;
				z-index: map-get-deep( $zIndex, 'card', 'text' );
				// Text
				color: #fff;
				font-size: 1.1em;
				padding: 5px 20px;
				white-space: nowrap;
			}
		}

		img {
			// Layout
			position: absolute;
			top: 50%;
			left: 50%;
			z-index: map-get-deep( $zIndex, 'card', 'img' );
			// Size
			width: 50%;
			height: auto;
			// Design
			opacity: 0.9;
			transform: translateY( -50% );
			transition: width map-get( $transitions, 'base' );

			@include breakpoint( 767px 0 ) {
				// Layout
				left: 60%;
				// Size
				width: 40%;
			}
		}
	}
}


.popup-wrap {
	// Layout
	display: none;
	position: fixed;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	z-index: map-get-deep( $zIndex, 'card', 'popup' );
	// Size
	width: 100%;
	height: 100%;
	// Text
	text-align: center;

	@include breakpoint( 991px 0 ) {
		// Layout
		padding-top: 10vh;
	}

	&.active {
		// Layout
		display: flex;
		// Design
		animation-name: fadeInFlex;
		animation-duration: map-get-deep( $transitions, 'base' );

		.popup-form-wrap {
			// Design
			animation-name: slideDown;
			animation-duration: map-get-deep( $transitions, 'base' );
		}
	}

	.popup-overlay {
		// Layout
		display: block;
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		z-index: map-get-deep( $zIndex, 'card', 'popup' );
		// Size
		width: 100%;
		height: 100%;
		// Design
		background-color: rgba(0, 0, 0, 0.5);
	}

	.popup-form-wrap {
		// Mixins
		@include closePopupButton( 40px );

		// Layout
		display: inline-block;
		margin: auto;
		position: relative;
		z-index: ( map-get-deep( $zIndex, 'card', 'popup' ) + 1 );
		// Size
		max-width: 95%;
		max-height: 90vh;
		// Design
		background-color: #fff;

		@include breakpoint( 991px 0 ) {
			// Size
			max-height: 80vh;
		}

		.scroll-wrap {
			// Layout
			padding: 15px 50px;
			// Size
			max-height: 90vh;
			// Design
			overflow-y: auto;
			overflow-x: hidden;

			@include breakpoint( 991px 0 ) {
				// Size
				max-height: 80vh;
			}

			@include breakpoint( 520px 0 ) {
				// Layout
				padding: 15px 25px;
			}

			.heading {
				// Layout
				margin-bottom: 30px;
				// Text
				text-transform: uppercase;

				@include breakpoint( 'max-height' 768px ) {
					// Layout
					margin-top: 0;
					margin-bottom: 10px;
				}

				span {
					// Layout
					display: block;
					// Text
					color: map-get-deep( $colors, 'brand', 'red' );
					font-weight: 300;
					font-family: Raleway;
				}
			}

			.wpcf7-form {

				.wpcf7-recaptcha
				{
					// Layout
					margin-bottom: 10px;

					& > div
					{
						// Size
						height: auto !important;
						width: 100% !important;
					}
				}

				& > p {

					@include breakpoint( 'max-height' 768px ) {
						// Layout
						margin-bottom: 5px;
					}
				}
			}

			.required-asterisk {
				// Text
				color: map-get-deep( $colors, 'brand', 'red' );
			}

			.wpcf7-form-control-wrap {
				// Layout
				display: block;

				input,
				select,
				textarea {
					// Size
					max-width: 100%;
				}
			}

			.wpcf7-submit {
				// Layout
				padding: 10px 25px;
				// Design
				background-color: map-get-deep( $colors, 'brand', 'red' );
				border: none;
				// Text
				color: #fff;
				font-size: 16px;
			}
		}

	}
}
